import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { Props } from "./Props";
import { SwitchScreen } from "../../Redux/GameDataReducer/Actions";
import { GetActivePlayer } from "../../Helper/StoreHelper";
import { LevelStore } from "../../Levels/LevelStore";
import { LevelModel } from "../../Levels/LevelModel";
import { LevelData, LevelState } from "../../Redux/GameDataReducer/PlayerReducer/LevelData";
import { LevelPreview } from "./LevelPreview/LevelPreview";
import { Move } from "../../Helper/SetupHelper";
import { AreEqual } from "../../Models/CellModel";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";

const LevelSelectScreenInternal: React.FC<Props> = (props) => {
    const [levelModels, setLevelModels] = useState<{ model: LevelModel; data: LevelData | undefined }[]>([]);
    useEffect(() => {
        const levels = LevelStore.GetStore().GetAllLevels();
        const models = levels.filter((l) => l.Cell.Row >= 0).map((l) => {
            const levelData = props.levels.find((ld) => ld.Id === l.Id);
            return { model: l, data: levelData };
        });

        const maxColumn = Math.max(...levels.map((l) => l.Cell.Column));
        const maxRow = Math.max(...levels.map((l) => l.Cell.Column));
        const setup = { NumberOfRows: maxRow, NumberOfColumns: maxColumn };
        const completedLevels = models.filter((lm) => lm.data?.State === LevelState.Completed).map((lm) => lm.model);
        for (const completedLevel of completedLevels) {
            for (const direction of completedLevel.OutputChannels.map((oc) => oc.Direction)) {
                let newCell = Move(setup, completedLevel.Cell, direction);
                while (newCell !== undefined) {
                    // eslint-disable-next-line
                    const nextLevel = models.find((cl) => AreEqual(cl.model.Cell, newCell!));
                    if (nextLevel !== undefined) {
                        nextLevel.model.InputChannels.push({ Channel: ChannelColorEnum.Green, Direction: direction, Status: "Exists", UseMachineColor: true });
                        break;
                    }

                    newCell = Move(setup, newCell, direction);
                }
            }
        }
        setLevelModels(models);
    }, [props.levels]);

    return <div style={{ position: "absolute", width: "100%" }}>
        <button style={{float: "right", marginRight: 10, marginTop: 10}} onClick={() => props.SwitchScreen("startMenu")}>Back</button>
        {levelModels.map((lm) => <LevelPreview Level={lm.model} Data={lm.data} key={lm.model.Id} />)}
    </div>
}

const mapStateToProps = (state: GameData) => {
    var levels = GetActivePlayer(state)!.levelData;
    return {
        levels
    };
};

export const LevelSelectScreen = connect(
    mapStateToProps,
    { SwitchScreen }
)(LevelSelectScreenInternal);