import React from "react";
import { FormatMessage } from "../../../MessageFormatter";
import { Props } from "./Props";
import { OutputStatus } from "../../../../Models/OutputStatusEnum";
import { ChannelColorEnum } from "../../../../Models/ChannelColorsEnum";

export const MessageDisplay: React.FC<Props> = (props) => {
    const onContentChanged: ((event: React.ChangeEvent<HTMLInputElement>) => void) = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (props.OnMessageChanged !== undefined) {
            props.OnMessageChanged({ Content: event.target.value, Channel: props.Message.Channel });
        }
    };
    const onChannelChanged = (event: React.ChangeEvent<HTMLSelectElement>) => {
        if (props.OnMessageChanged !== undefined) {
            props.OnMessageChanged({ Content: props.Message.Content, Channel: event.target.value as ChannelColorEnum });
        }
    };

    const style: React.CSSProperties = { display: "flex", flexFlow: "row", justifyContent: props.Input ? "flex-start" : "flex-end", marginBottom: 1 };
    const statusStyle: React.CSSProperties = { display: "flex", flexFlow: "row" };
    switch (props.Status) {
        case OutputStatus.Fullfilled:
            statusStyle.backgroundColor = "#d9ffb3";
            if (props.Editable) {
                statusStyle.borderRight = "solid green";
                statusStyle.borderBottom = "solid green";
            }
            break;
        case OutputStatus.NotFullfilled:
            // statusStyle.backgroundColor = "#ffb3b3";
            break;
        case OutputStatus.NotMatched:
            statusStyle.border = "solid red";
            statusStyle.backgroundColor = "#ffb3b3";
            break;
        default:
            break;
    }

    return (<div style={style}>
        <div style={statusStyle}>
            {props.Input && props.Editable && <button onClick={props.Remove}>-</button>}
            {props.Input && (props.Editable && props.AllowedInputColors.length > 1 ? <select value={props.Message.Channel} style={{ backgroundColor: props.Message.Channel }} onChange={onChannelChanged}>
                {props.AllowedInputColors.map((cc, index) => <option value={cc} key={index} style={{ backgroundColor: cc }}></option>)}
            </select> : <div style={{ backgroundColor: props.Message.Channel, width: 10 }} />)}
            {props.Editable ?
                <input value={props.Message.Content} onChange={onContentChanged}></input>
                :
                <label>{FormatMessage(props.Message.Content)}</label>}
            {!props.Input && <div style={{ backgroundColor: props.Message.Channel, width: 10 }} /> }
        </div>
    </div>);
};