import React from "react";
import { XYCoord } from "react-dnd";
import { connect } from "react-redux";
import { GetDirectedChannelSet } from "../../../Helper/NewChannelSet";
import { GetActivePlayer } from "../../../Helper/StoreHelper";
import { IsDefaultMachine } from "../../../MachineStore/IMachine";
import { MachineStore } from "../../../MachineStore/MachineStore";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { MachineCanvasHolder } from "../../Grid/Cell/MachineInstance/MachineDrawer/MachineCanvasHolder";

const DraggedMachineInternal: React.FC<{ machineId: string, offset: XYCoord, cellSize: number }> = (props) => {
    const machineType = MachineStore.GetStore().GetMachineType(props.machineId);
    const { inputChannels, outputChannels } = GetDirectedChannelSet(machineType.Api);
    const transform = `translate(${props.offset.x - props.cellSize / 2}px, ${props.offset.y - props.cellSize / 2}px)`
    return <div style={{ position: "absolute", transform, pointerEvents: "none" }}>
        <MachineCanvasHolder InputChannels={inputChannels}
            OutputChannels={outputChannels}
            MachineStyle={machineType.Style}
            BackgroundColor={"white"}
            CellSize={props.cellSize}
            IsDefault={IsDefaultMachine(props.machineId)} />
    </div>
};

const mapStateToProps = (state: GameData) => {

    return {
        cellSize: GetActivePlayer(state).cellSize
    };
};
export const DraggedMachine = connect(
    mapStateToProps,
    {}
)(DraggedMachineInternal);