import { LevelModel } from "./LevelModel";
import { AppendLength } from "./Levels/AppendLength";
import { GetLength } from "./Levels/GetLength";
import { GetType } from "./Levels/GetType";
import { IsEmpty } from "./Levels/IsEmpty";
import { KeepNumbers } from "./Levels/KeepNumbers";
import { PlusOne } from "./Levels/PlusOne";
import { RemoveRepeats } from "./Levels/RemoveRepeats";
import { RotateBackwards } from "./Levels/RotateBackwards";
import { Sandbox } from "./Levels/Sandbox";
import { ReverseLimited } from "./Levels/ReverseLimited";
import { Reverse } from "./Levels/Reverse";
import { CopyAll } from "./Levels/CopyAll";
import { Decrement } from "./Levels/Decrement";
import { IncrementAll } from "./Levels/IncrementAll";
import { IncrementIncreasing } from "./Levels/IncrementIncreasing";
import { JoinAll } from "./Levels/JoinAll";
import { MakeTwins } from "./Levels/MakeTwins";
import { RemoveIfDuplicate } from "./Levels/RemoveIfDuplicate";
import { RemoveUntilRepeat } from "./Levels/RemoveUntilRepeat";
import { Trigger } from "./Levels/Trigger";


export class LevelStore {
    private static levelStore: LevelStore;
    private levels: LevelModel[];

    private constructor() {
        this.levels = [];
        this.init();
    }

    public static GetStore(): LevelStore {
        if (LevelStore.levelStore === undefined) {
            LevelStore.levelStore = new LevelStore();
        }

        return LevelStore.levelStore;
    }

    private init(): void {
        this.levels.push(IsEmpty);
        this.levels.push(RemoveRepeats);
        this.levels.push(GetType);
        this.levels.push(KeepNumbers);
        this.levels.push(GetLength);
        this.levels.push(AppendLength);
        this.levels.push(RotateBackwards);
        this.levels.push(PlusOne);
        this.levels.push(ReverseLimited);
        this.levels.push(Reverse);
        this.levels.push(CopyAll);
        this.levels.push(Sandbox);
        this.levels.push(Decrement);
        this.levels.push(IncrementAll);
        this.levels.push(IncrementIncreasing);
        this.levels.push(JoinAll);
        this.levels.push(MakeTwins);
        this.levels.push(RemoveIfDuplicate, RemoveUntilRepeat, Trigger)
    }

    public GetAllLevels(): LevelModel[] {
        return this.levels;
    }

    public GetLevel(id: string): LevelModel {
        const level = this.levels.find((l) => l.Id === id);
        if (level === undefined) {
            throw Error(`Unable to find level with id '${id}'`)
        }

        return level;
    }
}