import { DragSourceMonitor, DragLayerMonitor, DropTargetMonitor, XYCoord } from "react-dnd";
import { DirectionEnum } from "../Models/DirectionEnum";
import { AppStore } from "../Redux/Store";
import { GetActivePlayer } from "./StoreHelper";

export function GetAngleFromMonitor(monitor: DragSourceMonitor | DragLayerMonitor | DropTargetMonitor, offsetX: number, offsetY: number): number | undefined {
    const xyCoord = monitor.getClientOffset();
    const xyCoordInit = monitor.getInitialSourceClientOffset();
    return GetAngleFromOffsets(offsetX, offsetY, xyCoord, xyCoordInit);
}

export function GetAngleFromOffsets(offsetX: number, offsetY: number, xyCoord: XYCoord | null, xyCoordInit: XYCoord | null): number | undefined {
    const center = GetActivePlayer(AppStore.getState()).cellSize / 2;
    if (xyCoord !== null && xyCoordInit !== null) {
        const angle = Math.atan2(xyCoord.y - xyCoordInit.y + offsetY - center, xyCoord.x - xyCoordInit.x + offsetX - center);
        return angle;
    }

    return undefined;
}

export function GetAngleFromDirection(direction: DirectionEnum): number {
    switch (direction) {
        case DirectionEnum.Up:
            return Math.PI * 0.5;
        case DirectionEnum.Right:
            return Math.PI * 1;
        case DirectionEnum.Down:
            return Math.PI * 1.5;
        case DirectionEnum.Left:
            return Math.PI * 2;
    }
}