import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { IActor } from "../IActor";
import { IMachine } from "../IMachine";
import { IMachineType } from "../IMachineType";
import { JoinActor } from "./JoinActor";

export class JoinMachine implements IMachine {
    public GetActor(): IActor {
        return new JoinActor();
    }
    public GetType(): IMachineType {
        return {
            Style: {
                BorderColor: "Orange",
                Color: "Grey",
                Name: "Join",
                Description: "Exits with the input from the green channel before the red channel",
            },
            Api: {
                InputChannels: [ChannelColorEnum.Green, ChannelColorEnum.Red],
                OutputChannels: [ChannelColorEnum.Green]
            },
            DependingOn: [],
            DirectlyDependingOn: []
        };
    }
    public id = "14";
}