import React, { useState } from "react";
import { ChangeCellSize } from "../../../Redux/GameDataReducer/PlayerReducer/Actions";
import { connect } from "react-redux";
import { IMachineType } from "../../../MachineStore/IMachineType";
import { GetDirectedChannelSet } from "../../../Helper/NewChannelSet";
import { MachineCanvasHolder } from "../../Grid/Cell/MachineInstance/MachineDrawer/MachineCanvasHolder";
import { GetActiveMachine, GetActivePlayer } from "../../../Helper/StoreHelper";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { GetLighterColor, GetRandomColor } from "../../../Helper/ColorHelper";
import { MachineApi } from "../../../Models/MachineApi";
import { ChannelColorEnum } from "../../../Models/ChannelColorsEnum";
import { MachineStyleModel } from "../../../Models/MachineStyleModel";

const ChangeSizeInternal: React.FC<{ MachineType: IMachineType | undefined; CellSize: number; ChangeCellSize: typeof ChangeCellSize; Close(): void }> = (props) => {

    const [size, setSize] = useState(props.CellSize);
    const [borderColor] = useState(GetRandomColor());
    const [backgroundColor] = useState(GetLighterColor(GetRandomColor(), 0.3));

    function SizeChanged(newSize: number) {
        props.ChangeCellSize(newSize);
        setSize(newSize);
    }

    const api: MachineApi = props.MachineType?.Api ?? { OutputChannels: [ChannelColorEnum.Green], InputChannels: [ChannelColorEnum.Green] };
    const style: MachineStyleModel = props.MachineType?.Style ?? { BorderColor: "red", Color: "green", Name: "Dummy", Description: "dummy" }
    const channels = GetDirectedChannelSet(api);
    return (
        <div style={{ margin: 10, width: 500, borderColor, backgroundColor, borderWidth: 20, borderStyle: "solid", padding: 30 }}>
            <div style={{ display: "flex", flexDirection: "column" }}>
                <div style={{ height: props.CellSize, width: props.CellSize, alignSelf: "center" }}>
                    <MachineCanvasHolder MachineStyle={style}
                        IsDefault={false}
                        BackgroundColor={backgroundColor}
                        InputChannels={channels.inputChannels}
                        OutputChannels={channels.outputChannels}
                        MovingOutput={undefined}
                        CellSize={props.CellSize} />
                </div>
                <input type="range" min="10" max="200" onChange={(event) => SizeChanged(Number(event.currentTarget.value))} value={size} />
                {size}
            </div>
            <button className="startMenu" onClick={props.Close}>Close</button>
        </div>
    );
};

const mapStateToProps = (state: GameData) => {
    const activeMachine = GetActiveMachine(state);
    const cellSize = GetActivePlayer(state).cellSize;
    return ({
        MachineType: activeMachine,
        CellSize: cellSize
    });
};

export const ChangeSizeComponent = connect(
    mapStateToProps,
    { ChangeCellSize }
)(ChangeSizeInternal);
