import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { MessageModel } from "../../Models/MessageModel";
import { IActor } from "../IActor";
import { IMachine } from "../IMachine";
import { IMachineType } from "../IMachineType";

export class CompareMachine implements IMachine {

    private actor: IActor = {
        Act: (message) => {
            const returnValue: MessageModel[] = [];
            let channel: ChannelColorEnum = ChannelColorEnum.Green;
            if (message.Content.length < 2) {
                channel = ChannelColorEnum.Red;
            } else {
                if (message.Content[0] !== "{") {
                    if (message.Content[0] !== message.Content[1]) {
                        channel = ChannelColorEnum.Red;
                    }
                } else {
                    const position = message.Content.indexOf("}");
                    const starNumber = message.Content.substring(0, position + 1);
                    if (message.Content.length < starNumber.length * 2) {
                        channel = ChannelColorEnum.Red;
                    } else {
                        const secondPart = message.Content.substring(position + 1, position + starNumber.length + 1);
                        if (secondPart !== starNumber) {
                            channel = ChannelColorEnum.Red;
                        }
                    }
                }
            }
            returnValue.push({ Content: message.Content, Channel: channel });
            return Promise.resolve(returnValue);
        }
    };
    public GetActor(): IActor {
        return this.actor;
    }
    public GetType(): IMachineType {
        return {
            Style: {BorderColor: "red",
            Color: "green",
            Name: "Compare",
            Description: "Compares the first two characters in the string. If they are the same, exits green. If they are different exits red"},
            Api: {
                InputChannels: [ChannelColorEnum.Green],
                OutputChannels: [ChannelColorEnum.Green, ChannelColorEnum.Red],
            },
            DependingOn: [],
            DirectlyDependingOn: []
        };
    }
    public id = "9";
}
