import { GridSetupActionTypes } from "./GridSetup/Actions";

interface AddNewMachineActionType {
    type: "AddNewMachineActionType";
}

export function AddNewMachine(): CustomMachinesActionTypes {
    return {
        type: "AddNewMachineActionType",
    };
}

interface DeleteMachineActionType {
    type: "DeleteMachineActionType";
    machineId: string;
}

export function DeleteMachine(machineId: string): CustomMachinesActionTypes {
    return {
        type: "DeleteMachineActionType",
        machineId
    };
}

interface LoadCustomMachineActionType {
    type: "LoadCustomMachineActionType";
    machineId: string;
}

export function LoadCustomMachine(machineId: string): CustomMachinesActionTypes {
    return {
        type: "LoadCustomMachineActionType",
        machineId
    };
}

interface ChangeCellSizeActionType {
    type: "ChangeCellSizeActionType";
    cellSize: number;
}

export function ChangeCellSize(cellSize: number): ChangeCellSizeActionType {
    return {
        type: "ChangeCellSizeActionType",
        cellSize
    };
}

export type CustomMachinesActionTypes = AddNewMachineActionType
    | LoadCustomMachineActionType
    | DeleteMachineActionType
    | GridSetupActionTypes
    | ChangeCellSizeActionType;
