import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { MessageModel } from "../../Models/MessageModel";
import { IActor } from "../IActor";
import { IMachine } from "../IMachine";
import { IMachineType } from "../IMachineType";

export class IncrementMachine implements IMachine {

    private actor: IActor = {
        Act: (message) => {
            const returnValue: MessageModel[] = [];
            let newMessage = "";
            if (message.Content.length > 0) {
                const char = message.Content[0];

                if ((char >= "0" && char <= "8") || (char >= "a" && char < "z") || (char >= "A" && char < "Z")) {
                    newMessage = String.fromCharCode(char.charCodeAt(0) + 1) + message.Content.slice(1);
                } else if (char === "9") {
                    newMessage = "0" + message.Content.slice(1);
                } else if (char === "z") {
                    newMessage = "a" + message.Content.slice(1);
                } else if (char === "Z") {
                    newMessage = "A" + message.Content.slice(1);
                } else {
                    newMessage = message.Content;
                }
            }

            returnValue.push({ Content: newMessage, Channel: ChannelColorEnum.Green });
            return Promise.resolve(returnValue);
        }
    };
    public GetActor(): IActor {
        return this.actor;
    }
    public GetType(): IMachineType {
        return {
            Style: {
                BorderColor: "DodgerBlue",
                Color: "MediumVioletRed",
                Name: "Increment",
                Description: "Increases a number in front of the string by 1, makes a letter the next letter in the alphabet. 9 becomes 0, z becomes a. All other starting characters are not changed"
            },
            Api: {
                InputChannels: [ChannelColorEnum.Green],
                OutputChannels: [ChannelColorEnum.Green],
            },
            DependingOn: [],
            DirectlyDependingOn: []
        };
    }
    public id = "12";
}
