import { ChannelColorEnum } from "../../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../../Models/DirectionEnum";
import { GridSetupReducer } from "./GridSetup/GridSetupReducer";
import { CustomMachineModel } from "./GridSetup/CustomMachineModel";
import { MachineApi, ApiAreEqual } from "../../../Models/MachineApi";
import { MachineInstanceModel } from "../../../Models/MachineInstanceModel";
import { Draft } from "immer";
import { GetRandomColor } from "../../../Helper/ColorHelper";
import { Actions } from "../../Actions";
import { CalculateExits, GetStartMachines } from "../../../Helper/SetupHelper";
import { UpdateMachineStatus } from "./CustomMachinesHelper";
import { TypeProvider } from "./TypeProvider";
import { PlayerModel } from "./PlayerModel";
import { CreateGuid } from "../../../Helper/Guid";
import { IsDefaultMachine } from "../../../MachineStore/IMachine";
import { LevelStore } from "../../../Levels/LevelStore";
import { LevelState } from "./LevelData";
import { Constants } from "../../../Components/Constants";

export function PlayerReducer(draft: Draft<PlayerModel>, action: Actions): PlayerModel {
    switch (action.type) {
        case "LoadLevelActionType":
            {
                const levelData = draft.levelData.find((ld) => ld.Id === action.id);
                if (levelData !== undefined) {
                    draft.activeMachineId = levelData.LastUsedMachineId;
                } else {
                    draft.levelData.push({ Id: action.id, State: LevelState.NoMachineLinked, LastUsedMachineId: undefined })
                    draft.activeMachineId = undefined;
                }
                draft.activeLevel = action.id;
                break;
            }
        case "ChangePlayerName":
            draft.name = action.name;
            break;
        case "UpdateShowAnimations":
            draft.showAnimations = action.show;
            break;
        case "UpdateStepTime":
            draft.stepTime = action.stepTime;
            break;
        case "AddNewMachineActionType":
            let newMachine: CustomMachineModel | undefined;

            const levelData = draft.levelData.find((ld) => ld.Id === draft.activeLevel!);
            if (levelData === undefined || levelData.LastUsedMachineId === undefined) {
                const levelModel = LevelStore.GetStore().GetLevel(draft.activeLevel!);
                newMachine = CreateNewMachine(levelModel.Name, levelModel.MachineDescription, levelModel.Style.Color, levelModel.Style.BorderColor);
            } else {
                newMachine = CreateNewMachine("New machine", "");
            }

            draft.machines.push(newMachine);
            draft.activeMachineId = newMachine.Id;

            if (levelData !== undefined) {
                if (levelData.LastUsedMachineId === undefined) {
                    levelData.LastUsedMachineId = newMachine.Id;
                }

                if (levelData.State === LevelState.NoMachineLinked) {
                    levelData.State = LevelState.InProgress;
                }
            }

            break;
        case "LoadCustomMachineActionType":
            const newActiveMachine = draft.machines.find((m) => m.Id === action.machineId);
            if (newActiveMachine === undefined) {
                throw Error("Loading an unknown machine, with id " + action.machineId);
            }
            draft.activeMachineId = action.machineId;
            break;
        case "ChangeCellSizeActionType":
            draft.cellSize = action.cellSize;
            break;
        case "DeleteMachineActionType":
            if (draft.activeMachineId === action.machineId) {
                draft.activeMachineId = undefined;
            }

            const usedInLevel = draft.levelData.find((ld) => ld.LastUsedMachineId === action.machineId);
            if (usedInLevel !== undefined) {
                usedInLevel.LastUsedMachineId = undefined;
                usedInLevel.State = LevelState.NoMachineLinked;
            }

            const directlyDependentMachines = draft.machines.filter((m) => m.DirectlyDependingOn.some((dp) => dp === action.machineId));
            for (const dependentMachine of directlyDependentMachines) {
                const updatedMachines: MachineInstanceModel[] = dependentMachine.Setup.MachineInstances.map((mi) => mi.MachineId === action.machineId ? { ...mi, Status: "Deleted" } : mi);
                dependentMachine.Setup.MachineInstances = updatedMachines;
                dependentMachine.DirectlyDependingOn = dependentMachine.DirectlyDependingOn.filter((i) => i !== action.machineId);
                dependentMachine.DependingOnDeleted.push(action.machineId);            
            }

            if (directlyDependentMachines.length > 0) {
                draft.deletedMachines.push(draft.machines.find((m) => m.Id === action.machineId)!);
            }

            const dependentMachines = draft.machines.filter((m) => m.DependingOn.some((dp) => dp === action.machineId));
            for (const dependentMachine of dependentMachines) {
                dependentMachine.DependingOn = dependentMachine.DependingOn.filter((i) => i !== action.machineId);
            }

            draft.machines = draft.machines.filter((m) => m.Id !== action.machineId);

            draft.deletedMachines = draft.deletedMachines.filter((dm) =>
                draft.machines.some((m) => m.DependingOnDeleted.some((dp) => dp === action.machineId))
            )
            break;
        default:
            if (draft.activeMachineId === undefined) {
                return draft;
            }

            const activeMachine = draft.machines.find((m) => m.Id === draft.activeMachineId)!;

            GridSetupReducer(activeMachine, action, new TypeProvider(draft.machines), draft.cellSize);
            break;
    }

    switch (action.type) {
        case "AddNewMachineActionType":
        case "ChannelChangedActionType":
        case "DeleteMachineActionType":
        case "FixMachineActionType":
        case "MachineAddedActionType":
        case "MachineMovedActionType":
        case "MachineRemovedActionType":
            if (draft.activeMachineId !== undefined) {
                EnrichState(draft);
            }
            break;
    }

    return draft;
};

function CreateNewMachine(name: string, description: string, color?: string, borderColor?: string): CustomMachineModel {
    return {
        Id: CreateGuid(),
        DirectlyDependingOn: [],
        Style: {
            BorderColor: borderColor ?? GetRandomColor(),
            Color: color ?? GetRandomColor(),
            Name: name,
            Description: description
        },
        Api: {
            InputChannels: [ChannelColorEnum.Green],
            OutputChannels: []
        },
        Setup: {
            NumberOfRows: Constants.MinimumRows,
            NumberOfColumns: Constants.MinimumColumns,
            MachineInstances: [{
                MachineId: "0",
                OutputChannels: [{ Direction: DirectionEnum.Right, Channel: ChannelColorEnum.Green, Status: "Exists" }],
                InputChannels: [], Id: 1,
                Cell: { Row: 2, Column: 1 },
                Status: "Valid"
            }]
        },
        ExitCells: [{ Cell: { Row: 2, Column: Constants.MinimumColumns - 1 }, Direction: DirectionEnum.Right }],
        DependingOn: ["0"],
        DependingOnDeleted: [],
        Status: "Valid"
    };
}

export function GetNewPlayer(name: string): PlayerModel {
    return {
        deletedMachines: [],
        id: CreateGuid(),
        levelData: [],
        machines: [],
        name,
        showAnimations: true,
        stepTime: 10,
        activeMachineId: undefined,
        activeLevel: undefined,
        cellSize: Constants.CellSize
    }
}

const EnrichState = (draft: PlayerModel) => {

    DetermineDependencies(draft);
    SetApi(draft);

    return draft;
};

function DetermineDependencies(draft: Draft<PlayerModel>) {
    const dependingDirectly: string[] = [];
    const depending: string[] = [];
    const DependingOnDeleted: string[] = [];
    const activeMachine = draft.machines.find((m) => m.Id === draft.activeMachineId)!;

    for (const machine of activeMachine.Setup.MachineInstances) {
        if (machine.Status === "Deleted") {
            DependingOnDeleted.push(machine.MachineId);
            continue;
        }
        if (!IsDefaultMachine(machine.MachineId)) {
            if (!dependingDirectly.some((d) => d === machine.MachineId)) {
                dependingDirectly.push(machine.MachineId);
            }
        }
        if (!depending.some((d) => d === machine.MachineId)) {
            depending.push(machine.MachineId);
            if (!IsDefaultMachine(machine.MachineId)) {
                const machineType = draft.machines.find((m) => m.Id === machine.MachineId)!;
                for (const depMachine of machineType.DependingOn) {
                    if (!depending.some((d) => d === depMachine)) {
                        depending.push(depMachine);
                    }
                }
            }
        }
    }
    activeMachine.DependingOn = depending;
    activeMachine.DirectlyDependingOn = dependingDirectly;
    return draft;
}

function SetApi(draft: Draft<PlayerModel>) {
    const activeMachine = draft.machines.find((m) => m.Id === draft.activeMachineId)!;

    activeMachine.Setup.MachineInstances.forEach((mi) => {
        mi.InputChannels.forEach((ic) => {
            ic.Status = "DoesNotExist";
        });
    });

    activeMachine.ExitCells = CalculateExits(activeMachine.Setup, new TypeProvider([...draft.machines, ...draft.deletedMachines]));

    const exitColors = activeMachine.ExitCells.map((ec) => {
        switch (ec.Direction) {
            case DirectionEnum.Right:
                return ChannelColorEnum.Green;
            case DirectionEnum.Up:
                return ChannelColorEnum.Blue;
            case DirectionEnum.Down:
                return ChannelColorEnum.Red;
            case DirectionEnum.Left:
                return ChannelColorEnum.Yellow;
            default:
                throw Error("Invalid value for direction");
        }
    });

    const outputs: ChannelColorEnum[] = [];
    for (const color of exitColors) {
        if (!outputs.some((o) => o === color)) {
            outputs.push(color);
        }
    }
    const startMachines = GetStartMachines(activeMachine.Setup);
    const inputs = startMachines.map((mi) => {
        switch (mi.MachineId) {
            case "0":
                return ChannelColorEnum.Green;
            case "1":
                return ChannelColorEnum.Red;
            case "2":
                return ChannelColorEnum.Blue;
            case "3":
                return ChannelColorEnum.Yellow;
            default:
                throw Error("Unknown startmachine " + mi.MachineId);
        }
    });
    const newApi: MachineApi = { InputChannels: inputs, OutputChannels: outputs };
    if (!ApiAreEqual(newApi, activeMachine.Api)) {
        UpdateStatusDependentMachines(draft, newApi);
    }
    activeMachine.Api = newApi;
    CheckMachineStatus(activeMachine);
    return draft;
}

function CheckMachineStatus(draft: Draft<CustomMachineModel>) {
    if (draft.Setup.MachineInstances.some((m) => m.Status !== "Valid")) {
        draft.Status = "Invalid";
    } else {
        draft.Status = "Valid";
    }
}

function UpdateStatusDependentMachines(draft: Draft<PlayerModel>, api: MachineApi) {
    const activeMachineId = draft.activeMachineId!;
    const dependentMachines = draft.machines.filter((m) => m.DirectlyDependingOn.some((dp) => dp === activeMachineId));
    for (const dependentMachine of dependentMachines) {
        for (const machineInstance of dependentMachine.Setup.MachineInstances.filter((mi) => mi.Status !== "Deleted" && mi.MachineId === activeMachineId)) {
            UpdateMachineStatus(api, machineInstance);
        }

        CheckMachineStatus(dependentMachine);
    }
}