import { DrawModel } from "./DrawModel";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { InputChannelModel } from "./InputChannelModel";
import { ChannelModel } from "../../../../../Models/ChannelModel";
import { DraggedOutputModel } from "./DraggedOutputModel";
import { GetAngleFromDirection } from "../../../../../Helper/AngleHelper";
import { DrawConstants } from "./DrawConstants";

export abstract class MachineDrawer {
    protected ctx: CanvasRenderingContext2D;
    protected innerColor: string;
    protected borderColor: string;
    protected inputChannels: InputChannelModel[];
    protected outputChannels: ChannelModel[][];
    protected draggedOutput: DraggedOutputModel | undefined;
    protected draggedDirection: DirectionEnum | undefined;
    protected backgroundColor: string;
    public drawConstants: DrawConstants;

    constructor(ctx: CanvasRenderingContext2D, props: DrawModel) {
        this.ctx = ctx;
        this.innerColor = props.MachineStyle.Color;
        this.borderColor = props.MachineStyle.BorderColor;
        this.inputChannels = props.InputChannels.sort((ic, id) => ic.Direction > id.Direction ? 1 : -1);
        this.outputChannels = props.OutputChannels;
        this.draggedOutput = props.MovingOutput;
        this.backgroundColor = props.BackgroundColor;
        this.drawConstants = new DrawConstants(props.CellSize);
        if (this.draggedOutput !== undefined) {
            if (this.draggedOutput !== undefined) {
                const adjustedAngle = this.draggedOutput.angle + Math.PI * 2.75;
                this.draggedDirection = Math.floor(adjustedAngle / (Math.PI * 0.5)) % 4;
            }
        }
    }

    public DrawMachine() {

        this.DrawBody();

        for (const input of this.inputChannels) {
            this.DrawInputGradient(input);
        }

        for (const outputGroup of this.outputChannels) {
            this.DrawOutputGroup(outputGroup, true);
        }

        if (this.draggedOutput !== undefined) {
            this.DraggedOutputDrawer(true);
        }

        if (this.inputChannels.length === 0) {
            this.DrawNoInputOutline();
        } else {
            let startIndex = 0;
            let endIndex = 0;
            do {
                endIndex = (endIndex + 1) % this.inputChannels.length;
                this.DrawInputOutline(this.inputChannels[startIndex].Direction, this.inputChannels[endIndex].Direction);
                startIndex++;
            } while (startIndex < this.inputChannels.length);
        }

        for (const outputGroup of this.outputChannels) {
            this.DrawOutputGroup(outputGroup, false);
        }

        if (this.draggedOutput !== undefined) {
            this.DraggedOutputDrawer(false);
        }
    }
    protected abstract DrawInputOutline(startDirection: DirectionEnum, endDirection: DirectionEnum): void;
    protected abstract DrawBody(): void;
    protected abstract DrawNoInputOutline(): void;
    protected abstract DrawInputGradient(input: InputChannelModel): void;

    protected SetShadowStyle() {
        this.ctx.shadowColor = "black";
        this.ctx.shadowOffsetX = this.drawConstants.shadeOffset;
        this.ctx.shadowOffsetY = this.drawConstants.shadeOffset;
    }

    protected ClearShadowStyle() {
        this.ctx.shadowColor = "transparent";
        this.ctx.shadowOffsetX = 0;
        this.ctx.shadowOffsetY = 0;
    }
    private DrawOutputGroup(outputs: ChannelModel[], isShade: boolean) {

        this.ClearShadowStyle();

        const baseAngle = GetAngleFromDirection(outputs[0].Direction) + Math.PI;
        const filteredOutputs = outputs.filter((c) => c.Status !== "DoesNotExist");
        let split = -(filteredOutputs.length - 1) / 2 * this.drawConstants.outputSplit;

        if (this.draggedDirection === outputs[0].Direction) {
            let increasedSplit = -(filteredOutputs.length) / 2 * this.drawConstants.outputSplit;
            let draggedIsDrawn = false;
            const draggedSplit = Math.sin(this.draggedOutput!.angle - baseAngle) * this.drawConstants.outputStartLength;
            for (let index = 0; index < filteredOutputs.length; index++) {
                if (!draggedIsDrawn && draggedSplit < split) {
                    draggedIsDrawn = true;
                    increasedSplit += this.drawConstants.outputSplit;
                }

                if (isShade) {
                    this.DrawOutputChannel("black", baseAngle, increasedSplit, this.drawConstants.shadeOffset * 2);
                } else {
                    this.DrawOutputChannel("black", baseAngle, increasedSplit, this.drawConstants.shadeOffset);
                    this.DrawOutputChannel(filteredOutputs[index].Channel, baseAngle, increasedSplit);
                }

                split += this.drawConstants.outputSplit;
                increasedSplit += this.drawConstants.outputSplit;
            }
        } else {
            for (const output of filteredOutputs) {
                if (isShade) {
                    this.DrawOutputChannel("black", baseAngle, split, this.drawConstants.shadeOffset * 2);
                } else {
                    this.DrawOutputChannel("black", baseAngle, split, this.drawConstants.shadeOffset);
                    this.DrawOutputChannel(output.Channel, baseAngle, split);
                }

                split += this.drawConstants.outputSplit;
            }
        }
    }

    private DraggedOutputDrawer(isShade: boolean) {
        this.ClearShadowStyle();
        if (isShade) {
            this.DrawDraggedOutput("black", this.drawConstants.shadeOffset * 2);
        } else {
            this.DrawDraggedOutput("black", this.drawConstants.shadeOffset);
            this.DrawDraggedOutput(this.draggedOutput!.channel.Channel, 0);
        }
    }

    private DrawDraggedOutput(color: string, offset: number) {
        const adjustedAngle = this.draggedOutput!.angle! + Math.PI * 0.25;
        const direction = Math.floor(adjustedAngle / (Math.PI * 0.5)) * Math.PI * 0.5;

        const firstX = this.drawConstants.center + this.drawConstants.outputStartLength * Math.cos(this.draggedOutput!.angle!) + offset;
        const firstY = this.drawConstants.center + this.drawConstants.outputStartLength * Math.sin(this.draggedOutput!.angle!) + offset;
        const endX = firstX + (this.drawConstants.outputLength - this.drawConstants.outputStartLength) * Math.cos(direction);
        const endY = firstY + (this.drawConstants.outputLength - this.drawConstants.outputStartLength) * Math.sin(direction);

        this.ctx.beginPath();
        this.ctx.moveTo(this.drawConstants.center, this.drawConstants.center);

        const gradient = this.ctx.createRadialGradient(this.drawConstants.center, this.drawConstants.center, 0, this.drawConstants.center, this.drawConstants.center, this.drawConstants.outputLength);
        gradient.addColorStop(0, this.innerColor);
        gradient.addColorStop(0.6, color);

        this.ctx.arcTo(firstX, firstY, endX, endY, this.drawConstants.outputRadius);
        this.ctx.lineTo(endX, endY);
        this.ctx.strokeStyle = gradient;
        this.ctx.lineWidth = this.drawConstants.outputWidth;

        this.ctx.stroke();
    }

    private DrawOutputChannel(color: string, mainAngle: number, outputOffset: number, offset = 0) {
        this.ctx.beginPath();
        this.ctx.moveTo(this.drawConstants.center, this.drawConstants.center);
        const differenceAngle = Math.asin(outputOffset / this.drawConstants.outputStartLength);

        const firstX = this.drawConstants.center + this.drawConstants.outputStartLength * Math.cos(mainAngle + differenceAngle) + offset;
        const firstY = this.drawConstants.center + this.drawConstants.outputStartLength * Math.sin(mainAngle + differenceAngle) + offset;
        const endX = firstX + (this.drawConstants.outputLength - this.drawConstants.outputStartLength) * Math.cos(mainAngle);
        const endY = firstY + (this.drawConstants.outputLength - this.drawConstants.outputStartLength) * Math.sin(mainAngle);

        const gradient = this.ctx.createRadialGradient(this.drawConstants.center, this.drawConstants.center, 0, this.drawConstants.center, this.drawConstants.center, this.drawConstants.outputLength)
        gradient.addColorStop(0, this.innerColor);
        gradient.addColorStop(0.6, color);

        this.ctx.arcTo(firstX, firstY, endX, endY, this.drawConstants.outputRadius);
        this.ctx.lineTo(endX, endY);
        this.ctx.strokeStyle = gradient;
        this.ctx.lineWidth = this.drawConstants.outputWidth;

        this.ctx.stroke();
    }
}