import React, { useState } from "react";
import { connect } from "react-redux";
import { StopPropagation } from "../../../Helper/ClickHelper";
import { GetLighterColor, GetRandomColor } from "../../../Helper/ColorHelper";
import { GetActiveMachine } from "../../../Helper/StoreHelper";
import { SwitchScreen } from "../../../Redux/GameDataReducer/Actions";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { ChangeSizeComponent } from "./ChangeSizeComponent";
import { EditMachine } from "./EditMachine/EditMachine";


const GameMenuInternal: React.FC<{ SwitchScreen: typeof SwitchScreen; Close(): void, hasActiveMachine: boolean }> = (props) => {

    const [dialog, setdialog] = useState<"none" | "editMachine" | "changeCellSize">("none");

    const [borderColor] = useState(GetRandomColor());
    const [backgroundColor] = useState(GetLighterColor(GetRandomColor(), 0.3));

    return (
        <div style={{ display: "flex", flexDirection: "row", alignItems: "center" }} onClick={StopPropagation}>
            {dialog !== "none" && <div style={{ width: 220 }} ></div>}
            <div style={{ borderColor, backgroundColor, borderWidth: 20, borderStyle: "solid", padding: 30, display: "flex", flexDirection: "column" }}>
                <button className="startMenu" onClick={() => props.SwitchScreen("startMenu")}>Back to menu</button>
                {props.hasActiveMachine && <button className="startMenu" onClick={() => setdialog("editMachine")}>Edit machine style</button>}
                <button className="startMenu" onClick={() => setdialog("changeCellSize")}>Change cell size</button>

                <button className="startMenu" onClick={props.Close}>Continue playing</button>
            </div>
            {dialog === "editMachine" && <EditMachine Close={() => setdialog("none")}/>}
            {dialog === "changeCellSize" && <ChangeSizeComponent Close={() => setdialog("none")}/>}

        </div>
    );
};

function mapStateToProps(state: GameData){
    const activeMachine = GetActiveMachine(state);
    return {hasActiveMachine: activeMachine !== undefined}
}

export const GameMenu = connect(
    mapStateToProps,
    { SwitchScreen }
)(GameMenuInternal);
