import React, { Dispatch } from "react";
import { useDrag } from "react-dnd";
import { connect } from "react-redux";
import { MachineStore } from "../../../../MachineStore/MachineStore";
import { ChannelModel } from "../../../../Models/ChannelModel";
import { ChangeChannel, MachineInstanceActionTypes, FixMachine, ChangeInputChannel } from "../../../../Redux/GameDataReducer/PlayerReducer/GridSetup/MachineInstances/Actions";
import { DragItemTypes } from "../../../DragItemTypes";
import { ChannelGroup } from "./ChannelClickArea/ChannelGroup";
import { Props } from "./Props";
import { GetLighterColor } from "../../../../Helper/ColorHelper";
import { MachineCanvasHolder } from "./MachineDrawer/MachineCanvasHolder";
import { DirectionEnum } from "../../../../Models/DirectionEnum";
import { InputChannelModel } from "./MachineDrawer/InputChannelModel";
import { InputChannelClickArea } from "./InputChannelClickArea/InputChannelClickArea";
import * as dc from "./MachineDrawer/DrawConstants";
import { IsDefaultMachine } from "../../../../MachineStore/IMachine";
import { GameData } from "../../../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../../../Helper/StoreHelper";

const MachineInstanceInternal: React.FC<Props> = (props) => {
    const [{ isDragging }, drag, preview] = useDrag({
        item: { type: DragItemTypes.MachineInstance, id: props.MachineInstanceId },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging(),
        }),
    });

    const OnClick = () => {
        if (props.MachineInstance.Status !== "Valid") {
            props.FixMachine();
        }
    };

    const api = MachineStore.GetStore().GetMachineType(props.MachineInstance.MachineId).Api;
    const onInputChannelClick = (direction: DirectionEnum) => {

        const channelIndex = props.MachineInstance.InputChannels.findIndex((ic) => ic.Direction === direction)!;
        const channel = props.MachineInstance.InputChannels[channelIndex];
        const currentIndex = api.InputChannels.indexOf(channel.Channel);
        const newChannel: ChannelModel = {
            Channel: currentIndex === api.InputChannels.length - 1 ? api.InputChannels[0] : api.InputChannels[currentIndex + 1],
            Direction: channel.Direction,
            Status: channel.Status
        };

        props.ChangeInputChannel(channelIndex, newChannel);
    };

    const machineStyle = props.MachineStyle ?? MachineStore.GetStore().GetMachineType(props.MachineInstance.MachineId).Style;

    const hasMultipleInputs = api.InputChannels.length > 1 || props.MachineInstance.InputChannels.some((ic) => ic.Status === "Exists" && ic.Channel !== api.InputChannels[0]);

    const inputChannels: InputChannelModel[] = props.MachineInstance.InputChannels.filter((ic) => ic.Status !== "DoesNotExist").map((c) =>
    ({
        UseMachineColor: !hasMultipleInputs,
        ...c
    })
    );

    const machineSize: React.CSSProperties = { height: props.CellSize, width: props.CellSize, margin: 1 };

    switch (props.MachineInstance.Status) {
        case "Deleted":
            machineSize.borderBottom = props.CellSize / 18 + "px dotted red";
            machineSize.boxSizing = "border-box";
            machineSize.opacity = 0.4;
            break;
        case "ChannelsInvalid":
            machineSize.borderBottom = props.CellSize / 18 + "px dotted orange";
            machineSize.boxSizing = "border-box";
            break;
    }

    const outputChannels: ChannelModel[][] = [];
    for (const channel of props.MachineInstance.OutputChannels) {
        const group = outputChannels.find((o) => o[0].Direction === channel.Direction);
        if (group === undefined) {
            outputChannels.push([channel]);
        } else {
            group.push(channel);
        }
    }

    const machineMargin = props.CellSize * 0.5 - dc.halfSquareFactor * props.CellSize;

    return (
        <div ref={preview} style={machineSize}>
            {!isDragging && <MachineCanvasHolder
                MachineStyle={machineStyle}
                IsDefault={IsDefaultMachine(props.MachineInstance.MachineId)}
                BackgroundColor={props.BackgroundColor}
                InputChannels={inputChannels}
                OutputChannels={outputChannels}
                MovingOutput={props.MovingOutput !== undefined ? props.MovingOutput : undefined}
                CellSize={props.CellSize}
            />}
            <div style={{ position: "absolute", marginLeft: machineMargin, marginTop: machineMargin, height: dc.halfSquareFactor * props.CellSize * 2, width: dc.halfSquareFactor * props.CellSize * 2 }} ref={drag} onClick={OnClick}></div>
            {inputChannels.filter((ic) => !ic.UseMachineColor).map((ic, index) => <InputChannelClickArea key={index} Direction={ic.Direction} OnClick={onInputChannelClick} cellSize={props.CellSize} />)}
            {outputChannels.map((oc, index) => <ChannelGroup key={index} ChannelModels={oc} OnChanged={props.ChangeOwnChannel} MachineInstanceId={props.MachineInstanceId} cellSize={props.CellSize}/>)}
        </div >
    );
};

const mapStateToProps = (state: GameData, ownProps: { MachineInstanceId: number }) => {
    const activeMachine = GetActiveMachine(state);

    const background = GetLighterColor(activeMachine!.Style.Color, 0.3);
    const MachineInstanceProp = activeMachine!.Setup.MachineInstances.find((mi) => mi.Id === ownProps.MachineInstanceId)!;
    const machineStyle = IsDefaultMachine(MachineInstanceProp.MachineId) ? undefined
        : MachineInstanceProp.Status === "Deleted" ? GetActivePlayer(state)!.deletedMachines.find((dm) => dm.Id === MachineInstanceProp.MachineId)!.Style
            : GetActivePlayer(state)!.machines.find((m) => m.Id === MachineInstanceProp.MachineId)!.Style;
    const MovingOutput = state.movingOutput?.machineInstanceId === ownProps.MachineInstanceId ? state.movingOutput : undefined;

    return {
        MachineInstance: MachineInstanceProp,
        MachineStyle: machineStyle,
        BackgroundColor: background,
        MovingOutput
    };
};

const mapDispatchToProps = (dispatch: Dispatch<MachineInstanceActionTypes>, ownProps: { MachineInstanceId: number }) => {

    return {
        ChangeOwnChannel: (channel: ChannelModel, angle?: number) => dispatch(ChangeChannel(ownProps.MachineInstanceId, channel, angle)),
        ChangeChannel: (machineInstanceId: number, channel: ChannelModel, angle?: number) => dispatch(ChangeChannel(machineInstanceId, channel, angle)),
        ChangeInputChannel: (index: number, channel: ChannelModel) => dispatch(ChangeInputChannel(ownProps.MachineInstanceId, index, channel)),
        FixMachine: () => dispatch(FixMachine(ownProps.MachineInstanceId))
    };
};

export const MachineInstance = connect(
    mapStateToProps,
    mapDispatchToProps
)(MachineInstanceInternal);
