import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { DrawModel } from "./DrawModel";
import { InputChannelModel } from "./InputChannelModel";
import { MachineDrawer } from "./MachineDrawer";



export class MachineSquareDrawer extends MachineDrawer {
    private closeSide: number;
    private farSide : number;
    private innerClose : number;
    private innerFar : number;
    private armProtrudingClose : number;
    private armProtrudingFar: number;
    private armWidthClose : number;
    private armWidthFar: number;

    constructor(ctx: CanvasRenderingContext2D, props: DrawModel) {
        super(ctx, props);
        this.closeSide = this.drawConstants.center - this.drawConstants.halfSquareSize;
        this.farSide = this.drawConstants.center + this.drawConstants.halfSquareSize;
        this.innerClose = this.drawConstants.center - 0.5 * this.drawConstants.aperture;
        this.innerFar = this.drawConstants.center + 0.5 * this.drawConstants.aperture;
        this.armProtrudingClose = this.closeSide - Math.cos(this.drawConstants.armAngle) * this.drawConstants.armLength;
        this.armProtrudingFar = this.farSide + Math.cos(this.drawConstants.armAngle) * this.drawConstants.armLength;
        this.armWidthClose = this.innerClose - Math.sin(this.drawConstants.armAngle) * this.drawConstants.armLength;
        this.armWidthFar = this.innerFar + Math.sin(this.drawConstants.armAngle) * this.drawConstants.armLength;
    }
    protected DrawBody() {
        this.ctx.beginPath();
        this.ClearShadowStyle();
        this.ctx.moveTo(this.drawConstants.center + this.drawConstants.circleRadius, this.drawConstants.center);
        this.ctx.rect(this.drawConstants.center - this.drawConstants.halfSquareSize, this.drawConstants.center - this.drawConstants.halfSquareSize, this.drawConstants.halfSquareSize * 2, this.drawConstants.halfSquareSize * 2);
        this.ctx.fillStyle = this.innerColor;
        this.ctx.fill();
        this.ctx.beginPath();
    }
    protected DrawInputOutline(startDirection: DirectionEnum, endDirection: DirectionEnum): void {
        this.ctx.beginPath();
        this.ctx.strokeStyle = this.borderColor;
        this.SetShadowStyle();
        this.ctx.lineWidth = this.drawConstants.borderWidth;

        switch (startDirection) {
            case DirectionEnum.Down:
                this.ctx.moveTo(this.armWidthFar, this.armProtrudingClose);
                this.ctx.lineTo(this.innerFar, this.closeSide);
                break;
            case DirectionEnum.Left:
                this.ctx.moveTo(this.armProtrudingFar, this.armWidthFar);
                this.ctx.lineTo(this.farSide, this.innerFar);
                break;
            case DirectionEnum.Up:
                this.ctx.moveTo(this.armWidthClose, this.armProtrudingFar);
                this.ctx.lineTo(this.innerClose, this.farSide);
                break;
            case DirectionEnum.Right:
                this.ctx.moveTo(this.armProtrudingClose, this.armWidthClose);
                this.ctx.lineTo(this.closeSide, this.innerClose);
                break;
            default:
                throw Error("Unknown direction");
        }

        let currentDirection = startDirection;

        do {
            switch (currentDirection) {
                case DirectionEnum.Down:
                    this.ctx.lineTo(this.farSide, this.closeSide);
                    break;
                case DirectionEnum.Left:
                    this.ctx.lineTo(this.farSide, this.farSide);
                    break;
                case DirectionEnum.Up:
                    this.ctx.lineTo(this.closeSide, this.farSide);
                    break;
                case DirectionEnum.Right:
                    this.ctx.lineTo(this.closeSide, this.closeSide);
                    break;
                default:
                    throw Error("Unknown direction");
            }

            currentDirection = (currentDirection + 1) % 4;
        } while (currentDirection !== endDirection);

        switch (endDirection) {
            case DirectionEnum.Down:
                this.ctx.lineTo(this.innerClose, this.closeSide);
                this.ctx.lineTo(this.armWidthClose, this.armProtrudingClose);
                break;
            case DirectionEnum.Left:
                this.ctx.lineTo(this.farSide, this.innerClose);
                this.ctx.lineTo(this.armProtrudingFar, this.armWidthClose);
                break;
            case DirectionEnum.Up:
                this.ctx.lineTo(this.innerFar, this.farSide);
                this.ctx.lineTo(this.armWidthFar, this.armProtrudingFar);
                break;
            case DirectionEnum.Right:
                this.ctx.lineTo(this.closeSide, this.innerFar);
                this.ctx.lineTo(this.armProtrudingClose, this.armWidthFar);
                break;
            default:
                throw Error("Unknown direction");
        }

        this.ctx.stroke();
    }

    protected DrawNoInputOutline(): void {
        this.ctx.beginPath();
        this.ctx.moveTo(this.drawConstants.center + this.drawConstants.circleRadius, this.drawConstants.center);
        this.ctx.rect(this.drawConstants.center - this.drawConstants.halfSquareSize, this.drawConstants.center - this.drawConstants.halfSquareSize, this.drawConstants.halfSquareSize * 2, this.drawConstants.halfSquareSize * 2);
        this.ctx.strokeStyle = this.borderColor;
        this.SetShadowStyle();
        this.ctx.lineWidth = this.drawConstants.borderWidth;

        this.ctx.stroke();
    }
    protected DrawInputGradient(input: InputChannelModel): void {
        this.ctx.beginPath();
        this.ClearShadowStyle();
        let grd: CanvasGradient;
        switch (input.Direction) {
            case DirectionEnum.Down:
                this.ctx.moveTo(this.innerClose, this.closeSide + 0.4 * this.drawConstants.borderWidth);
                this.ctx.lineTo(this.armWidthClose, this.armProtrudingClose);
                this.ctx.lineTo(this.armWidthFar, this.armProtrudingClose);
                this.ctx.lineTo(this.innerFar, this.closeSide + 0.4 * this.drawConstants.borderWidth);
                this.ctx.lineTo(this.innerClose, this.closeSide + 0.4 * this.drawConstants.borderWidth);
                grd = this.ctx.createLinearGradient(this.drawConstants.center, this.closeSide + 0.4 * this.drawConstants.borderWidth, this.drawConstants.center, this.armProtrudingClose);
                break;
            case DirectionEnum.Left:
                this.ctx.moveTo(this.farSide - 0.4 * this.drawConstants.borderWidth, this.innerClose);
                this.ctx.lineTo(this.armProtrudingFar, this.armWidthClose);
                this.ctx.lineTo(this.armProtrudingFar, this.armWidthFar);
                this.ctx.lineTo(this.farSide - 0.4 * this.drawConstants.borderWidth, this.innerFar);
                this.ctx.lineTo(this.farSide - 0.4 * this.drawConstants.borderWidth, this.innerClose);
                grd = this.ctx.createLinearGradient(this.farSide - 0.4 * this.drawConstants.borderWidth, this.drawConstants.center, this.armProtrudingFar, this.drawConstants.center);
                break;
            case DirectionEnum.Up:
                this.ctx.moveTo(this.innerFar, this.farSide - 0.4 * this.drawConstants.borderWidth);
                this.ctx.lineTo(this.armWidthFar, this.armProtrudingFar);
                this.ctx.lineTo(this.armWidthClose, this.armProtrudingFar);
                this.ctx.lineTo(this.innerClose, this.farSide - 0.4 * this.drawConstants.borderWidth);
                this.ctx.lineTo(this.innerFar, this.farSide - 0.4 * this.drawConstants.borderWidth);
                grd = this.ctx.createLinearGradient(this.drawConstants.center, this.farSide - 0.4 * this.drawConstants.borderWidth, this.drawConstants.center, this.armProtrudingFar);
                break;
            case DirectionEnum.Right:
                this.ctx.moveTo(this.closeSide + 0.4 * this.drawConstants.borderWidth, this.innerFar);
                this.ctx.lineTo(this.armProtrudingClose, this.armWidthFar);
                this.ctx.lineTo(this.armProtrudingClose, this.armWidthClose);
                this.ctx.lineTo(this.closeSide + 0.4 * this.drawConstants.borderWidth, this.innerClose);
                this.ctx.lineTo(this.closeSide + 0.4 * this.drawConstants.borderWidth, this.innerFar);
                grd = this.ctx.createLinearGradient(this.closeSide + 0.4 * this.drawConstants.borderWidth, this.drawConstants.center, this.armProtrudingClose, this.drawConstants.center);
                break;
            default:
                throw Error("Unknown direction");
        }

        if (input.UseMachineColor) {
            grd.addColorStop(0, this.innerColor);
        } else {
            grd.addColorStop(0, input.Channel);
            grd.addColorStop(0.9, input.Channel);
            grd.addColorStop(0.9, this.backgroundColor);
        }
        grd.addColorStop(1, this.backgroundColor);
        this.ctx.fillStyle = grd;
        this.ctx.fill();
    }
}