import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { MessageModel } from "../../Models/MessageModel";
import { IActor } from "../IActor";
import { IMachine } from "../IMachine";
import { IMachineType } from "../IMachineType";

export class BlueStartMachine implements IMachine {
    private actor: IActor = {
        Act: (message) => {
            const returnValue: MessageModel[] = [];
            returnValue.push({ Content: message.Content, Channel: ChannelColorEnum.Green });
            return Promise.resolve(returnValue);
        }
    };
    public GetActor(): IActor {
        return this.actor;
    }
    public GetType(): IMachineType {
        return {
            Style: {
                Color: "white",
                BorderColor: "blue",
                Name: "Start",
                Description: "Starting point of the computation. Acts as a move machine for all other purposes"
            },
            Api: {
                InputChannels: [ChannelColorEnum.Green],
                OutputChannels: [ChannelColorEnum.Green],
            },
            DependingOn: [],
            DirectlyDependingOn: []
        };
    }
    public readonly id = "2";
}