import React, { useState } from "react";
import { SetName, SetDescription, SetColor, SetBorderColor } from "../../../../Redux/GameDataReducer/PlayerReducer/GridSetup/Actions";
import { Props } from "./Props";
import { connect } from "react-redux";
import { GameData } from "../../../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../../../Helper/StoreHelper";
import { MachineCanvasHolder } from "../../../Grid/Cell/MachineInstance/MachineDrawer/MachineCanvasHolder";
import { GetDirectedChannelSet } from "../../../../Helper/NewChannelSet";
import { GetLighterColor, GetRandomColor } from "../../../../Helper/ColorHelper";

const EditMachineInternal: React.FC<Props> = (props) => {
    const [color, setColor] = useState<string>(props.MachineType.Style.Color);
    const [borderColor, setBorderColor] = useState<string>(props.MachineType.Style.BorderColor);
    const [dialogBorderColor] = useState(GetRandomColor());
    const [backgroundColor] = useState(GetLighterColor(GetRandomColor(), 0.3));

    function colorChanged(color: string): void {
        setColor(color);
        props.SetColor(color);
    }

    function borderChanged(borderColor: string): void {
        setBorderColor(borderColor);
        props.SetBorderColor(borderColor);
    }

    const channels = GetDirectedChannelSet(props.MachineType.Api);
    return (
        <div style={{ margin: 10, width: 500, borderColor: dialogBorderColor, backgroundColor, borderWidth: 20, borderStyle: "solid", padding: 30 }}>
            {props.MachineType !== undefined &&
                <div style={{ display: "flex", flexDirection: "column" }}>
                    <div style={{ height: props.CellSize, width: props.CellSize, alignSelf: "center" }}>
                        <MachineCanvasHolder MachineStyle={props.MachineType.Style}
                            IsDefault={false}
                            BackgroundColor={backgroundColor}
                            InputChannels={channels.inputChannels}
                            OutputChannels={channels.outputChannels}
                            MovingOutput={undefined}
                            CellSize={props.CellSize} />
                    </div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                        <label>Name</label>
                        <input onChange={(event) => props.SetName(event.target.value)} value={props.MachineType.Style.Name}></input>
                    </div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                        <label>Color</label>
                        <input onChange={(event) => colorChanged(event.target.value)} value={color}></input>
                    </div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                        <label>BorderColor</label>
                        <input onChange={(event) => borderChanged(event.target.value)} value={borderColor}></input>
                    </div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                        <label>Description</label>
                        <textarea cols={50} rows={5} onChange={(event) => props.SetDescription(event.target.value)} value={props.MachineType.Style.Description}></textarea>
                    </div>
                </div>}
                <button className="startMenu" onClick={props.Close}>Close</button>
        </div>
    );
};

const mapStateToProps = (state: GameData) => {
    const activeMachine = GetActiveMachine(state);
    const cellSize = GetActivePlayer(state).cellSize;
    return ({
        MachineType: activeMachine!,
        CellSize: cellSize
    });
};

export const EditMachine = connect(
    mapStateToProps,
    { SetName, SetDescription, SetColor, SetBorderColor }
)(EditMachineInternal);
