import React from "react";
import { connect } from "react-redux";
import NumberInput from "../NumberInput/NumberInput";
import { Props } from "./Props";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { GetActivePlayer } from "../../Helper/StoreHelper";
import { UpdateStepTime, UpdateShowAnimations } from "../../Redux/GameDataReducer/Actions";
import { Computer } from "../../Computing/Computer";

const DashboardInternal: React.FC<Props> = (props) => {

    const onClicked = () => {
        props.UpdateShowAnimations(!props.ShowAnimations);
    };
    return (
        <div style={{ display: "flex", justifyContent: "space-between", width: "100%", height: "100%" }}>
            <div style={{paddingRight: 30}}>
                <button onClick={Computer.StopComputation} title="Stop the running computation">Stop</button>
            </div>
            <div style={{ display: "flex", flexDirection: "column" }}>
                <label title="Show animations, will calculate as fast as possible when turned of">Animations</label>
                {props.ShowAnimations && <label title="Step time in tenths of seconds, lower for faster animations">Step Time</label>}
            </div>

            <div style={{ display: "flex", flexDirection: "column", width: 100 }}>
                <button onClick={onClicked} style={{ marginLeft: 5, width: 63}}>{props.ShowAnimations ? "On" : "Off"}</button>
                {props.ShowAnimations && <NumberInput value={props.StepTime} valueChanged={props.UpdateStepTime} />}
            </div>

        </div>
    );
};

const mapStateToProps = (state: GameData) => {
    return {
        ShowAnimations: GetActivePlayer(state)!.showAnimations,
        StepTime: GetActivePlayer(state)!.stepTime,
    };
};

export const Dashboard = connect(
    mapStateToProps,
    {
        UpdateStepTime, UpdateShowAnimations
    }
)(DashboardInternal);