import React from "react";
import { Props } from "./Props";
import { StoredMachine } from "./StoredMachine/StoredMachine";
import { AddNewMachine } from "../../Redux/GameDataReducer/PlayerReducer/Actions";
import { connect } from "react-redux";
import { MachineStore } from "../../MachineStore/MachineStore";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../Helper/StoreHelper";
import { IsStartMachine, IsDefaultMachine } from "../../MachineStore/IMachine";

const MachineCollectionInternal: React.FC<Props> = (props) => {

    const storedMachines = props.allowedMachines.map((m) => <div key={m}><StoredMachine machine={m} /></div>);

    return (
        <div style={{ overflow: "auto", height: props.height, position: "absolute" }}>
            <button onClick={props.AddNewMachine}>Create new machine</button>
            {storedMachines}
        </div>
    );
};

const mapStateToProps = (state: GameData) => {
    const store = MachineStore.GetStore();
    const machines = store.GetAllMachines();
    let allowedMachines: string[] = [];
    const disallowedMachines: string[] = [];
    const allowedStartMachines: string[] = [];

    for (const machine of machines) {
        if (state.activeLevel!.allowedMachines.some((am) => machine === am)) {
            if (IsStartMachine(machine)) {
                allowedStartMachines.push(machine);
            }
            else {
                allowedMachines.push(machine);
            }
        } else {
            disallowedMachines.push(machine);
        }
    }

    const activeMachine = GetActiveMachine(state);
    if (activeMachine !== undefined) {
        const usedStartMachines = activeMachine.Setup.MachineInstances.filter((m) => IsStartMachine(m.MachineId));

        const nextStartMachine = allowedStartMachines.find((m) => !usedStartMachines.some((s) => s.MachineId === m));
        if (nextStartMachine !== undefined) {
            allowedMachines = [nextStartMachine, ...allowedMachines];
        }
    }

    const allowedCustomMachines: string[] = GetActivePlayer(state)!.machines.filter((customMachine) => !customMachine.DependingOn.some((dm) => IsDefaultMachine(dm) && disallowedMachines.some((dis) => dis === dm))).map((cm) => cm.Id);
    allowedMachines = allowedMachines.concat(allowedCustomMachines);

    return {
        allowedMachines
    };
};

export const MachineCollection = connect(
    mapStateToProps,
    { AddNewMachine }
)(MachineCollectionInternal);