import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { InputChannelModel } from "./InputChannelModel";
import { MachineDrawer } from "./MachineDrawer";
import { GetAngleFromDirection } from "../../../../../Helper/AngleHelper";

export class MachineCircleDrawer extends MachineDrawer {
    protected DrawBody() {
        this.ctx.beginPath();
        this.ClearShadowStyle();
        this.ctx.moveTo(this.drawConstants.center + this.drawConstants.circleRadius, this.drawConstants.center);
        this.ctx.arc(this.drawConstants.center, this.drawConstants.center, this.drawConstants.circleRadius, 0, Math.PI * 2);
        this.ctx.fillStyle = this.innerColor;
        this.ctx.fill();
    }
    
    protected DrawInputOutline(startDirection: DirectionEnum, endDirection: DirectionEnum): void {
        const localcenter = this.drawConstants.center;
        const startDirectionAngle = GetAngleFromDirection(startDirection);
        const startAngle = startDirectionAngle + this.drawConstants.apertureAngle;

        const startInnerX = localcenter + this.drawConstants.circleRadius * Math.cos(startAngle);
        const startInnerY = localcenter + this.drawConstants.circleRadius * Math.sin(startAngle);
        const startOuterX = startInnerX + this.drawConstants.armLength * Math.cos(startDirectionAngle + this.drawConstants.armAngle);
        const startOuterY = startInnerY + this.drawConstants.armLength * Math.sin(startDirectionAngle + this.drawConstants.armAngle);

        const endDirectionAngle = GetAngleFromDirection(endDirection);
        const endAngle = endDirectionAngle - this.drawConstants.apertureAngle;
        const endInnerX = localcenter + this.drawConstants.circleRadius * Math.cos(endAngle);
        const endInnerY = localcenter + this.drawConstants.circleRadius * Math.sin(endAngle);

        const endOuterX = endInnerX + this.drawConstants.armLength * Math.cos(endDirectionAngle - this.drawConstants.armAngle);
        const endOuterY = endInnerY + this.drawConstants.armLength * Math.sin(endDirectionAngle - this.drawConstants.armAngle);

        this.ctx.beginPath();
        this.ctx.moveTo(startOuterX, startOuterY);
        this.ctx.lineTo(startInnerX, startInnerY);
        this.ctx.arc(localcenter, localcenter, this.drawConstants.circleRadius, startAngle, endAngle);
        this.ctx.lineTo(endOuterX, endOuterY);

        this.ctx.strokeStyle = this.borderColor;
        this.ctx.lineWidth = this.drawConstants.borderWidth;
        this.SetShadowStyle();
        this.ctx.stroke();

    }

    protected DrawNoInputOutline(): void {
        this.ctx.beginPath();
        this.ctx.moveTo(this.drawConstants.center + this.drawConstants.circleRadius, this.drawConstants.center);
        this.ctx.arc(this.drawConstants.center, this.drawConstants.center, this.drawConstants.circleRadius, 0, Math.PI * 2);

        this.ctx.strokeStyle = this.borderColor;
        this.SetShadowStyle();
        this.ctx.lineWidth = this.drawConstants.borderWidth;

        this.ctx.stroke();
    }
    protected DrawInputGradient(input: InputChannelModel): void {

        const directionAngle = GetAngleFromDirection(input.Direction);
        const endAngle = directionAngle - this.drawConstants.apertureAngle;

        const endInnerX = this.drawConstants.center + this.drawConstants.circleRadius * Math.cos(endAngle);
        const endInnerY = this.drawConstants.center + this.drawConstants.circleRadius * Math.sin(endAngle);

        const endOuterX = endInnerX + this.drawConstants.armLength * Math.cos(directionAngle - this.drawConstants.armAngle);
        const endOuterY = endInnerY + this.drawConstants.armLength * Math.sin(directionAngle - this.drawConstants.armAngle);

        const startAngle = directionAngle + this.drawConstants.apertureAngle;

        const startInnerX = this.drawConstants.center + this.drawConstants.circleRadius * Math.cos(startAngle);
        const startInnerY = this.drawConstants.center + this.drawConstants.circleRadius * Math.sin(startAngle);

        const startOuterX = startInnerX + this.drawConstants.armLength * Math.cos(directionAngle + this.drawConstants.armAngle);
        const startOuterY = startInnerY + this.drawConstants.armLength * Math.sin(directionAngle + this.drawConstants.armAngle);

        this.ctx.beginPath();

        this.ClearShadowStyle();
        this.ctx.moveTo(endInnerX, endInnerY);
        const endOuterAngle = Math.atan2(endOuterY - this.drawConstants.center, endOuterX - this.drawConstants.center);
        const startOuterAngle = Math.atan2(startOuterY - this.drawConstants.center, startOuterX - this.drawConstants.center);

        this.ctx.arc(this.drawConstants.center, this.drawConstants.center, this.drawConstants.circleRadius + this.drawConstants.protrudingArm, endOuterAngle, startOuterAngle);
        this.ctx.arc(this.drawConstants.center, this.drawConstants.center, this.drawConstants.circleRadius - this.drawConstants.borderWidth * 0.5, startAngle, endAngle, true);

        const grd = this.ctx.createRadialGradient(this.drawConstants.center, this.drawConstants.center, this.drawConstants.circleRadius, this.drawConstants.center, this.drawConstants.center, this.drawConstants.circleRadius + this.drawConstants.protrudingArm);

        if (input.UseMachineColor) {
            grd.addColorStop(0, this.innerColor);
        } else {
            grd.addColorStop(0, input.Channel);
            grd.addColorStop(0.7, input.Channel);
            grd.addColorStop(0.8, this.backgroundColor);
        }
        grd.addColorStop(1, this.backgroundColor);
        this.ctx.fillStyle = grd;
        this.ctx.fill();
    }
}