import { ChannelColorEnum } from "./ChannelColorsEnum";

export interface MachineApi {
    readonly OutputChannels: ChannelColorEnum[];
    readonly InputChannels: ChannelColorEnum[];
}

export function ApiAreEqual(first: MachineApi, second: MachineApi): boolean {
    if (first.InputChannels.length !== second.InputChannels.length) {
        return false;
    }
    if (first.OutputChannels.length !== second.OutputChannels.length) {
        return false;
    }
    if (first.InputChannels.some((i) => !second.InputChannels.some((o) => i === o))) {
        return false;
    }
    if (first.OutputChannels.some((i) => !second.OutputChannels.some((o) => i === o))) {
        return false;
    }

    return true;
}