import React from "react";
import { Constants } from "../../../Constants";
import { Props } from "./Props";
import { GetDirectedChannelSet } from "../../../../Helper/NewChannelSet";
import { MachineCanvasHolder } from "../../../Grid/Cell/MachineInstance/MachineDrawer/MachineCanvasHolder";

const size = Constants.CellSize * 0.33;
export const MachineDisplay: React.FC<Props> = (props) => {

    const { inputChannels, outputChannels } = GetDirectedChannelSet(props.MachineType.Api);
    
    return (
        <div style={{display:"flex"}}>
            <div style={{height:size , width: size}}>
                <MachineCanvasHolder
                    MachineStyle={props.MachineType.Style}
                    IsDefault={props.IsDefault}
                    BackgroundColor={"White"}
                    InputChannels={inputChannels}
                    OutputChannels={outputChannels}
                    CellSize={size}
                />
            </div>
            <label>{props.MachineType.Style.Name}</label>
        </div>);
};
