export function CheckColorString(color: string): boolean {
    if (color === "" || color === undefined || color === null) {
        return false;
    }

    const style = new Option().style;
    style.color = color;

    return style.color === color.toLowerCase();
}

function GetColorModel(color: string): ColorModel {
    if (color.startsWith("rgb(")) {
        const colorPart = color.split("(")[1].split(")")[0];
        const colors = colorPart.split(",");
        return {
            red: Number(colors[0]),
            green: Number(colors[1]),
            blue: Number(colors[2])
        }
    } else if (!color.startsWith("#")) {
        color = GetColorCode(color);
    }

    return {
        red: parseInt(color.substring(1, 3), 16),
        blue: parseInt(color.substring(5, 7), 16),
        green: parseInt(color.substring(3, 5), 16)
    }
}

export function GetOpaqueColor(color: string, opacity: number): string {
    try {
        const model = GetColorModel(color);

        return `rgba(${model.red},${model.green},${model.blue},${opacity})`;
    } catch (error) {
        return "white";
    }
}

export function GetLighterColor(color: string, amount: number): string {
    try {
        const model = GetColorModel(color);
        const red = 256 - (256 - model.red) * amount;
        const blue = 256 - (256 - model.blue) * amount;
        const green = 256 - (256 - model.green) * amount;
        return `rgb(${red},${green},${blue})`;
    } catch (error) {
        return "white";
    }
}


export function GetGrayScaleColor(color: string, amount: number): string {
    const model = GetColorModel(color);

    try {
        const average = (model.red + model.green + model.blue) / 3
        return `rgb(${average * amount + model.red * (1 - amount)},${average * amount + model.green * (1 - amount)},${average * amount + model.blue * (1 - amount)})`;
    } catch (error) {
        return "white";
    }
}

