import { CellModel } from "../../../../../Models/CellModel";
import { ChannelModel } from "../../../../../Models/ChannelModel";
import { MachineApi } from "../../../../../Models/MachineApi";

interface ChannelChangedActionType {
    type: "ChannelChangedActionType";
    machineInstanceId: number;
    channel: ChannelModel;
    angle?: number;
}

export function ChangeChannel(machineInstanceId: number, channel: ChannelModel, angle?: number): MachineInstanceActionTypes {
    return {
        type: "ChannelChangedActionType",
        machineInstanceId,
        channel,
        angle
    };
}

interface InputChannelChangedActionType {
    type: "InputChannelChangedActionType";
    machineInstanceId: number;
    channelIndex: number;
    channel: ChannelModel;
}

export function ChangeInputChannel(machineInstanceId: number, channelIndex: number, channel: ChannelModel): MachineInstanceActionTypes {
    return {
        type: "InputChannelChangedActionType",
        machineInstanceId,
        channelIndex,
        channel
    };
}

interface MachineRemovedActionType {
    type: "MachineRemovedActionType";
    machineInstanceId: number;
}

export function RemoveMachine(machineInstanceId: number): MachineInstanceActionTypes {
    return {
        type: "MachineRemovedActionType",
        machineInstanceId
    };
}

interface MachineAddedActionType {
    type: "MachineAddedActionType";
    machineId: string;
    cell: CellModel;
    api: MachineApi;
}

export function AddMachine(machineId: string, api: MachineApi, cellId: CellModel): MachineInstanceActionTypes {
    return {
        type: "MachineAddedActionType",
        machineId,
        cell: cellId,
        api
    };
}

interface MachineMovedActionType {
    type: "MachineMovedActionType";
    machineInstanceId: number;
    newCell: CellModel;
}

export function MoveMachine(machineInstanceId: number, newCell: CellModel): MachineInstanceActionTypes {
    return {
        type: "MachineMovedActionType",
        machineInstanceId,
        newCell
    };
}

interface FixMachineActionType {
    type: "FixMachineActionType";
    machineInstanceId: number;
}

export function FixMachine(machineInstanceId: number): MachineInstanceActionTypes {
    return {
        type: "FixMachineActionType",
        machineInstanceId
    };
}

export type MachineInstanceActionTypes = ChannelChangedActionType |
    MachineRemovedActionType |
    MachineAddedActionType |
    MachineMovedActionType |
    FixMachineActionType |
    InputChannelChangedActionType;
