import { IActor } from "./IActor";
import { IMachineType } from "./IMachineType";

export interface IMachine {
    GetActor(): IActor;
    GetType(): IMachineType;
    id: string;
}

export function IsDefaultMachine(id: string): boolean {
    return id.length < 6;
}

export function IsStartMachine(id: string): boolean {
    return id === "0" || id === "1" || id === "2" || id === "3";
}