import { MachineInstanceModel } from "../../../../../Models/MachineInstanceModel";
import { Draft } from "immer";
import { Actions } from "../../../../Actions";
import { GetNewChannelSet } from "../../../../../Helper/NewChannelSet";
import { AreEqual } from "../../../../../Models/CellModel";
import * as dc from "../../../../../Components/Grid/Cell/MachineInstance/MachineDrawer/DrawConstants";
import { GetAngleFromDirection } from "../../../../../Helper/AngleHelper";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { ITypeProvider } from "../../../../../MachineStore/ITypeProvider";
import { IsStartMachine } from "../../../../../MachineStore/IMachine";

export function MachineInstancesReducer(draft: Draft<MachineInstanceModel[]>, action: Actions, typeProvider: ITypeProvider, cellSize: number): MachineInstanceModel[] {
    switch (action.type) {
        case "ChannelChangedActionType":
            const machine = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            const channelIndex = machine.OutputChannels.findIndex((c) => c.Channel === action.channel.Channel);
            const relevantChannels = machine.OutputChannels.filter((c, index) => c.Direction === action.channel.Direction && c.Status !== "DoesNotExist" && index !== channelIndex);
            if (relevantChannels.length > 0 && action.angle !== undefined) {
                const newChannelArray = machine.OutputChannels.filter((c) => c.Direction !== action.channel.Direction && c.Status !== "DoesNotExist");
                let split = -(relevantChannels.length - 1) / 2 * dc.outputSplitFactor * cellSize;

                let inserted = false;
                const baseAngle = GetAngleFromDirection(action.channel.Direction);
                const draggedSplit = -Math.sin(action.angle - baseAngle) * dc.outputStartLengthFactor * cellSize;
                for (let index = 0; index < relevantChannels.length; index++) {
                    if (!inserted && draggedSplit < split) {
                        inserted = true;
                        newChannelArray.push(action.channel);
                    }

                    newChannelArray.push(relevantChannels[index]);
                    split += dc.outputSplitFactor * cellSize;
                }

                if (!inserted) {
                    newChannelArray.push(action.channel);
                }

                machine.OutputChannels = newChannelArray;

            } else {
                machine.OutputChannels[channelIndex] = action.channel;
            }
            return draft;
        case "InputChannelChangedActionType":
            const machineInput = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            const channelModel = machineInput.InputChannels.find((ic) => ic.Direction === action.channel.Direction)!;
            channelModel.Direction = machineInput.InputChannels[action.channelIndex].Direction;
            machineInput.InputChannels[action.channelIndex] = action.channel;
            return draft;
        case "MachineRemovedActionType":
            const machineRemoved = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            if (IsStartMachine(machineRemoved.MachineId) && draft.filter((mi) => IsStartMachine(mi.MachineId)).length < 2) {
                return draft;
            } else {
                return draft.filter((mi) => mi.Id !== action.machineInstanceId);
            }
        case "MachineAddedActionType":
            if (draft.some((mi) => AreEqual(mi.Cell, action.cell))) {
                return draft;
            }

            const { outputChannels, inputChannels } = GetNewChannelSet(action.api, false);

            const maxId = (draft.length === 0) ? 0 : Math.max(...draft.map((mi) => mi.Id));
            const machineInstance: MachineInstanceModel = {
                Cell: action.cell,
                MachineId: action.machineId,
                OutputChannels: outputChannels,
                InputChannels: inputChannels,
                Id: maxId + 1,
                Status: "Valid"
            };

            draft.push(machineInstance);
            return draft;

        case "MachineMovedActionType":
            if (draft.some((m) => AreEqual(m.Cell, action.newCell))) {
                return draft;
            }
            const machineMoved = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            machineMoved.Cell = action.newCell;
            return draft;
        case "FixMachineActionType":
            const machineFixed = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            if (machineFixed.Status === "Deleted") {
                return draft.filter((mi) => mi.Id !== action.machineInstanceId);
            } else if (machineFixed.Status === "ChannelsInvalid") {
                const api = typeProvider.GetMachineType(machineFixed.MachineId).Api;
                machineFixed.OutputChannels = machineFixed.OutputChannels.filter((c) => api.OutputChannels.some((o) => o === c.Channel));
                machineFixed.OutputChannels = machineFixed.OutputChannels.concat(api.OutputChannels.filter((o) => !machineFixed.OutputChannels.some((c) => c.Channel === o)).map((o) => ({ Channel: o, Direction: DirectionEnum.Up, Status: "Exists" })));

                machineFixed.InputChannels = machineFixed.InputChannels.map((ic) => {
                    if (ic.Status === "Exists" && !api.InputChannels.some((iac) => iac === ic.Channel)) {
                        return { ...ic, Channel: api.InputChannels[0] };
                    } else {
                        return ic;
                    }
                });

                machineFixed.Status = "Valid";
            }
            return draft;
        default:
            return draft;
    }
}