import { MachineApi } from "../Models/MachineApi";
import { ChannelModel } from "../Models/ChannelModel";
import { DirectionEnum } from "../Models/DirectionEnum";
import { InputChannelModel } from "../Components/Grid/Cell/MachineInstance/MachineDrawer/InputChannelModel";

export function GetNewChannelSet(api: MachineApi, includeInput: boolean): { inputChannels: ChannelModel[], outputChannels: ChannelModel[] } {
    const outputChannels: ChannelModel[] = [];
    const inputChannels: ChannelModel[] = [];

    let direction: DirectionEnum = DirectionEnum.Up;
    for (const color of api.OutputChannels) {
        outputChannels.push({ Channel: color, Direction: direction, Status: "Exists" });
        direction++;
    }

    if (includeInput && api.InputChannels.length > 1) {
        direction = 0;
        for (const color of api.InputChannels) {
            inputChannels.push({ Channel: color, Direction: direction, Status: "Exists" });
            direction++;
        }
    }

    return { inputChannels, outputChannels };
}

export function GetDirectedChannelSet(api: MachineApi): { inputChannels: InputChannelModel[], outputChannels: ChannelModel[][] }{
    const { inputChannels, outputChannels } = GetNewChannelSet(api, true);

    const hasMultipleInputs = inputChannels.length > 1;

    const inputChannelModels: InputChannelModel[] = inputChannels.map((c) =>
        ({
            UseMachineColor: !hasMultipleInputs,
            ...c
        })
    );

    const orderedOutputChannels: ChannelModel[][] = [];
    for (const channel of outputChannels) {
        const group = orderedOutputChannels.find((o) => o[0].Direction === channel.Direction);
        if (group === undefined) {
            orderedOutputChannels.push([channel]);
        } else {
            group.push(channel);
        }
    }

    return {inputChannels: inputChannelModels, outputChannels: orderedOutputChannels}
}