import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../Helper/StoreHelper";
import { useDrag, useDragLayer, XYCoord } from "react-dnd";
import { DragItemTypes } from "../DragItemTypes";
import { getEmptyImage } from "react-dnd-html5-backend";

const MovingElementInternal: React.FC<{
    Rows: number;
    Columns: number;
    CellSize: number;
    WindowHeight: number;
    WindowWidth: number; 
    children?: React.ReactNode;
}> = (props) => {
    const calculatedHeight = props.CellSize * (props.Rows - .35);
    const calculatedWidth = props.CellSize * (props.Columns - 0.35);

    const [leftStart, setLeftStart] = useState((props.WindowWidth - calculatedWidth) / 2);
    const [topStart, setTopStart] = useState((props.WindowHeight - calculatedHeight) / 2);
    const [finalOffset, setFinalOffset] = useState<XYCoord>({ x: 0, y: 0 });
    const [{ isDragging }, drag, preview] = useDrag({
        item: { type: DragItemTypes.Grid },
        end: (result, monitor) => {
            const offSet = monitor.getDifferenceFromInitialOffset();
            if (offSet !== null) {
                setFinalOffset(offSet);
            }
        },
        collect: (monitor) => ({ isDragging: monitor.isDragging() })
    });

    useEffect(() => {
        setLeftStart((l) => l + finalOffset.x);
        setTopStart((t) => t + finalOffset.y);
    }, [finalOffset])

    const offSet = useDragLayer((monitor) => {
        if (monitor.getItemType() === DragItemTypes.Grid) {
            const offSet = monitor.getDifferenceFromInitialOffset();
            if (offSet !== null) {
                return offSet;
            } else {
                return undefined;
            }
        }
    });

    useEffect(() => {
        preview(getEmptyImage(), { captureDraggingState: true })
    }, [preview])

    const left = isDragging && offSet !== undefined ? leftStart + offSet.x : leftStart;
    const top = isDragging && offSet !== undefined ? topStart + offSet.y : topStart;

    return (
        <div ref={drag} style={{ position: "absolute", top, left, height: calculatedHeight, width: calculatedWidth }}>
            {props.children}
        </div>
    );
};

function mapStateToProps(state: GameData) {
    const activeMachine = GetActiveMachine(state);
    const CellSize = GetActivePlayer(state).cellSize;
    return {
        Rows: activeMachine!.Setup.NumberOfRows,
        Columns: activeMachine!.Setup.NumberOfColumns,
        Style: activeMachine!.Style,
        CellSize
    };
}

export const MovingElement = connect(
    mapStateToProps
)(MovingElementInternal);