import React from "react";
import { connect } from "react-redux";
import { Cell } from "./Cell/Cell";
import { GetLighterColor } from "../../Helper/ColorHelper";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../Helper/StoreHelper";
import { MachineStyleModel } from "../../Models/MachineStyleModel";

const GridInternal: React.FC<{
    Rows: number;
    Columns: number;
    CellSize: number;
    Style: MachineStyleModel;
}> = (props) => {
    const rows = [];
    for (let rowIndex = 0; rowIndex < props.Rows; rowIndex++) {
        const row = [];
        for (let columnIndex = 0; columnIndex < props.Columns; columnIndex++) {

            row.push(<Cell key={columnIndex} cellId={{ Row: rowIndex, Column: columnIndex }} smallHeigth={rowIndex === 0 || rowIndex === props.Rows - 1} smallWidth={columnIndex === 0 || columnIndex === props.Columns - 1} />);
        }
        rows.push(<div key={rowIndex} style={{ display: "flex", flexDirection: "row", opacity: 1 }}>{row}</div>);
    }

    return (
        <div style={{ position: "absolute", borderColor: GetLighterColor(props.Style.BorderColor, 0.4), borderWidth: props.CellSize * 0.2, borderStyle: "solid", marginBottom: 0 }}>
            <div style={{ display: "flex", flexDirection: "column", backgroundColor: GetLighterColor(props.Style.Color, 0.3) }}>
                {rows}
            </div>
        </div>
    );
};

function mapStateToProps(state: GameData) {
    const activeMachine = GetActiveMachine(state);
    const CellSize = GetActivePlayer(state).cellSize;
    return {
        Rows: activeMachine!.Setup.NumberOfRows,
        Columns: activeMachine!.Setup.NumberOfColumns,
        Style: activeMachine!.Style,
        CellSize
    };
}

export const Grid = connect(
    mapStateToProps
)(GridInternal);