import React, { useRef, useEffect } from "react";
import { DrawModel } from "./DrawModel";
import { MachineCircleDrawer } from "./MachineCircleDrawer";
import { MachineDrawer } from "./MachineDrawer";
import { MachineSquareDrawer } from "./MachineSquareDrawer";

export const MachineCanvasHolder: React.FC<DrawModel> = (props) => {
    const canvasRef = useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        if (canvasRef.current) {
            const ctx = canvasRef.current.getContext("2d")!;
            ctx.clearRect(0, 0, props.CellSize, props.CellSize);
            let drawer: MachineDrawer;
            if (props.IsDefault) {
                drawer = new MachineCircleDrawer(ctx, props);
            } else {
                drawer = new MachineSquareDrawer(ctx, props);
            }

            drawer.DrawMachine();
        }
    });

    return <div style={{
        height: props.CellSize,
        width: props.CellSize,
        position: "absolute"
    }}>
        <canvas ref={canvasRef} style={{ position: "absolute" }} height={props.CellSize} width={props.CellSize}></canvas>
    </div>;
};