export function Minimum<T>(array: T[], valueSelector: (element: T) => number): T | undefined {
    let minimum = Infinity;
    let minimumElement: T | undefined = undefined;
    for (const element of array) {
        const value = valueSelector(element);
        if (value < minimum) {
            minimumElement = element;
            minimum = value;
        }
    }

    return minimumElement;
}

export function Maximum<T>(array: T[], valueSelector: (element: T) => number): T | undefined {
    let maximum = -Infinity;
    let maximumElement: T | undefined = undefined;
    for (const element of array) {
        const value = valueSelector(element);
        if (value > maximum) {
            maximumElement = element;
            maximum = value;
        }
    }

    return maximumElement;
}