import { IActor } from "../IActor";
import { MessageModel } from "../../Models/MessageModel";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";

export class JoinActor implements IActor {
    private greenInput: string | undefined;
    private redInput: string | undefined;
    public Act(message: MessageModel): Promise<MessageModel[]> {
        switch (message.Channel) {
            case ChannelColorEnum.Green:
                this.greenInput = message.Content;
                break;
            case ChannelColorEnum.Red:
                this.redInput = message.Content;
                break;
            default:
                throw Error("Machine does not have inputchannel " + message.Channel);
        }

        if (this.greenInput === undefined || this.redInput === undefined) {
            return Promise.resolve([]);
        }
        const returnMessage = this.greenInput.concat(this.redInput);
        this.greenInput = undefined;
        this.redInput = undefined;
        const returnValue: MessageModel[] = [{ Channel: ChannelColorEnum.Green, Content: returnMessage }];

        return Promise.resolve(returnValue);
    }
}