const halfSquareRatio = 0.9;
const circleRadiusRatio = 0.28;
export const halfSquareFactor = halfSquareRatio * circleRadiusRatio;
export const outputSplitFactor = 0.12;
export const outputStartLengthFactor = 0.25;

export class DrawConstants {

    constructor(cellSize: number) {
        this.cellSize = cellSize;
        this.aperture = 0.23 * cellSize;
        this.armLength = 0.18 * cellSize;
        this.circleRadius = circleRadiusRatio * cellSize;
        this.borderWidth = 0.05 * cellSize;
        this.halfSquareSize = halfSquareRatio * this.circleRadius;
        this.shadeOffset = 0.008 * cellSize;
        this.outputSplit = outputSplitFactor * cellSize;
        this.outputLength = 0.38 * cellSize;
        this.outputRadius = 0.15 * cellSize;
        this.outputWidth = 0.06 * cellSize;
        this.outputStartLength = outputStartLengthFactor * cellSize;
        this.center = 0.5 * cellSize;
        this.apertureAngle = Math.asin(this.aperture * 0.5 / this.circleRadius);
        this.defiatingAngle = this.armAngle - this.apertureAngle;
        this.protrudingArm = Math.cos(this.defiatingAngle) * this.armLength;
    }

    public cellSize: number;
    public aperture: number;
    public armLength: number;
    public armAngle = Math.PI * 0.05;
    public circleRadius: number;
    public borderWidth: number;
    public halfSquareSize: number;
    public shadeOffset: number;
    public outputSplit: number;
    public outputLength: number;
    public outputRadius: number;
    public outputWidth: number;
    public outputStartLength: number;
    public center: number;
    public apertureAngle: number;
    public defiatingAngle: number;
    public protrudingArm: number;
}