import React from "react";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { Props } from "./Props";
import { ChannelClickArea } from "./ChannelClickArea";

export const widthFactor = 0.07;
export const heightFactor = 0.31;
export const marginFactor = 0.05;

export const ChannelGroup: React.FC<Props> = (props) => {

    if (props.ChannelModels.length === 0) {
        return <></>;
    }

    const width = props.cellSize * widthFactor;
    const height = props.cellSize * heightFactor;
    const heightOffset = props.cellSize * 0.06;
    const margin = props.cellSize * marginFactor;

    let baseOffsetX = (props.cellSize - (props.ChannelModels.length * (width + margin) - margin)) / 2;
    let baseOffsetY = props.cellSize / 2 - height - heightOffset;
    let deltaX = width + margin;
    let deltaY = 0;

    const channelSetStyle: React.CSSProperties = {
        position: "absolute",
        marginLeft: baseOffsetX - margin,
        marginTop: baseOffsetY,
        display: "flex",
        flexDirection: "row"
    };

    let rotate: string = "";

    switch (props.ChannelModels[0].Direction) {
        case DirectionEnum.Down:
            rotate = "rotate(180deg)";
            baseOffsetY = props.cellSize / 2 + heightOffset;
            baseOffsetX = props.cellSize - baseOffsetX - width;
            deltaX = -deltaX;
            break;
        case DirectionEnum.Left:
            rotate = "rotate(270deg)";
            const tempBaseOffsetX = baseOffsetY;
            baseOffsetY = props.cellSize - baseOffsetX - width;
            baseOffsetX = tempBaseOffsetX;
            deltaY = -deltaX;
            deltaX = 0;
            break;
        case DirectionEnum.Right:
            rotate = "rotate(90deg)";
            baseOffsetY = baseOffsetX;
            baseOffsetX = props.cellSize / 2 + heightOffset;
            deltaY = deltaX;
            deltaX = 0;
            break;
    }

    const rotation: React.CSSProperties = {
        transform: rotate,
        height: props.cellSize,
        width: props.cellSize,
        position: "absolute",
        pointerEvents: "none"
    };

    return (
        <div style={rotation}>
            <div style={channelSetStyle}>
                {props.ChannelModels.map((cm, index) => <ChannelClickArea key={index} Channel={cm} OnClick={props.OnChanged} MachineInstanceId={props.MachineInstanceId} OffsetX={baseOffsetX + deltaX * index} OffsetY={baseOffsetY + deltaY * index} CellSize={props.cellSize} />)}
            </div>
        </div>
    );
}
