import React, { useEffect, useState } from "react";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { Dashboard } from "../Dashboard/Dashboard";
import { Grid } from "../Grid/Grid";
import { LevelDisplay } from "../LevelDisplay/LevelDisplay";
import { MachineCollection } from "../MachineCollection/MachineCollection";
import { Computer } from "../../Computing/Computer";
import { AddNewMachine } from "../../Redux/GameDataReducer/PlayerReducer/Actions";
import { RemoveMachine } from "../../Redux/GameDataReducer/PlayerReducer/GridSetup/MachineInstances/Actions";
import { connect } from "react-redux";
import { Props } from "./Props";
import { ClearMessages } from "../../Redux/GameDataReducer/PlayerReducer/GridSetup/Actions";
import { GetActiveMachine } from "../../Helper/StoreHelper";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { useDragLayer, useDrop } from "react-dnd";
import { DraggedNewMachine, DraggedOutputChannel, DragItemTypes } from "../DragItemTypes";
import { GetAngleFromMonitor } from "../../Helper/AngleHelper";
import { SetMovingOutput } from "../../Redux/GameDataReducer/Actions";
import { GameMenu } from "./GameMenu/GameMenu";
import { Constants } from "../Constants";
import { DraggedMachine } from "../MachineCollection/DraggedMachine/DraggedMachine";
import { MovingElement } from "../Grid/MovingElement";

const GameInternal: React.FC<Props> = (props) => {
    const [menu, setMenu] = useState<"none" | "options">("none");

    const [height, setHeight] = useState(window.innerHeight);
    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        document.addEventListener("keyup", handleKeyPress, false);
        window.addEventListener("resize", handleResize);
        return () => {
            document.removeEventListener("keyup", handleKeyPress, false);
            window.removeEventListener("resize", handleResize, false);

        }
    })

    const [, drop] = useDrop({
        accept: [DragItemTypes.MachineInstance],
        drop: (item, monitor) => {
            if (monitor.isOver()) {
                switch (item.type) {
                    case DragItemTypes.MachineInstance:
                        props.RemoveMachine(monitor.getItem().id);
                        break;
                }
            }
        },
    });

    const dragPreviewElement = useDragLayer((monitor) => {
        switch (monitor.getItemType()) {
            case DragItemTypes.OutputChannel:

                const outputItem: DraggedOutputChannel = monitor.getItem() as DraggedOutputChannel;
                const draggedOutputAngle = GetAngleFromMonitor(monitor, outputItem.offsetX, outputItem.offsetY);
                if (draggedOutputAngle !== undefined) {
                    props.SetMovingOutput({ angle: draggedOutputAngle, ...outputItem });
                }
                break;
            case DragItemTypes.NewMachine:
                if (monitor.isDragging()) {
                    const offset = monitor.getClientOffset();
                    if (offset !== null) {
                        const item = monitor.getItem() as DraggedNewMachine;
                        return <DraggedMachine machineId={item.id} offset={offset} />
                    }
                }
                break
        }
        return null;
    });

    function handleKeyPress(event: KeyboardEvent): void {
        switch (event.key) {
            case " ":
                Computer.StopComputation();
                props.ClearMessages();
                event.stopPropagation();
                break;
            default:
                break;
        }
    }

    function handleResize(): void {
        setHeight(window.innerHeight);
        setWidth(window.innerWidth);
    }

    const allowedInputColors = [ChannelColorEnum.Green];
    const opagueBackground = "rgba(255,255,255,.8)";
    return (
        <div ref={drop} style={{ position: "absolute", height, width, overflow: "hidden" }}>
            <div style={{ position: "absolute", height: "100%", width: "100%" }}>
                {!props.HasActiveMachine ?
                    <div style={{ display: "flex", height: "100%", width: "100%", justifyContent: "center", alignItems: "center" }}>
                        <button title={"Add new machine"} onClick={props.AddNewMachine}>Create new machine</button>
                    </div>
                    :
                    <MovingElement WindowHeight={height} WindowWidth={width}><Grid /></MovingElement>
                }
            </div >
            <div style={{ position: "absolute", left: width / 2 - 150, padding: 10, width: 300, backgroundColor: opagueBackground }}>
                <h2 style={{ textAlign: "center", margin: 0 }}>{props.MachineName}</h2>
            </div>
            <div style={{ position: "absolute", padding: 10, left: width - 220, width: 180, height: height - 20, backgroundColor: opagueBackground }}>
                <button onClick={() => setMenu("options")} title="Options">Options</button>
                <MachineCollection height={height - 40} />
            </div>
            <div style={{ position: "absolute", padding: 10, left: 10, top: 50, width: 300, backgroundColor: opagueBackground }}>
                <LevelDisplay MultipleInputsAllowed={false} AllowedInputs={allowedInputColors} />
            </div>
            <div style={{ position: "absolute", padding: 10, height: 80, width: 240, left: width / 2 - 150, top: height - 80, backgroundColor: opagueBackground }}>
                <Dashboard />
            </div>
            {menu === "options" &&
                <div onClick={() => setMenu("none")} style={{ position: "absolute", height: "100%", width: "100%", display: "flex", justifyContent: "center", alignItems: "center", background: Constants.MenuBackGround }}>
                    <GameMenu Close={() => setMenu("none")} />
                </div>
            }
            {dragPreviewElement}
        </div>
    );
}

const mapStateToProps = (state: GameData) => {
    const activeMachine = GetActiveMachine(state);

    return {
        HasActiveMachine: activeMachine !== undefined,
        MachineName: activeMachine?.Style.Name
    };
};
export const Game = connect(
    mapStateToProps,
    { AddNewMachine, ClearMessages, SetMovingOutput, RemoveMachine }
)(GameInternal);