import { ChannelModel } from "../../../../../Models/ChannelModel";
import React, { useEffect } from "react";
import { heightFactor, widthFactor, marginFactor } from "./ChannelGroup";
import { useDrag } from "react-dnd";
import { DragItemTypes, DraggedOutputChannel } from "../../../../DragItemTypes";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { getEmptyImage } from "react-dnd-html5-backend";
import { GetAngleFromMonitor } from "../../../../../Helper/AngleHelper";

export const ChannelClickArea: React.FC<{
    Channel: ChannelModel,
    MachineInstanceId: number,
    OnClick: (channel: ChannelModel, angle?: number) => void,
    OffsetX: number,
    OffsetY: number,
    CellSize: number
}> = (props) => {

    const [{ isDragging }, drag, preview] = useDrag<DraggedOutputChannel, unknown, { isDragging: boolean }>({
        item: { type: DragItemTypes.OutputChannel, channel: props.Channel, machineInstanceId: props.MachineInstanceId, offsetX: props.OffsetX, offsetY: props.OffsetY},
        begin: () => {
            props.OnClick({ ...props.Channel, Status: "DoesNotExist" });
        },
        end: (dropResult, monitor) => {
            if (!monitor.didDrop()) {
                const outputItem = monitor.getItem() as DraggedOutputChannel;
                const angle = GetAngleFromMonitor(monitor, props.OffsetX, props.OffsetY);
                if (angle !== undefined) {
                    const adjustedAngle = angle + Math.PI * 2.75;
                    const direction = Math.floor(adjustedAngle / (Math.PI * 0.5)) % 4;
                    props.OnClick({ Direction: direction, Channel: props.Channel.Channel, Status: outputItem.channel.Status }, angle);
                }
            }
        },
        collect: (monitor) => ({ isDragging: monitor.isDragging() })
    });

    const onClicked = () => {
        const newDirection: DirectionEnum = props.Channel.Direction === 3 ? 0 : props.Channel.Direction + 1;
        props.OnClick({ Direction: newDirection, Channel: props.Channel.Channel, Status: props.Channel.Status });
    };

    const channelStyle: React.CSSProperties = {
        width: widthFactor * props.CellSize,
        marginLeft: marginFactor * props.CellSize,
        opacity: isDragging ? 0 : 1,
        height: heightFactor*props.CellSize,
        pointerEvents: "all"
    };

    useEffect(() => {
        preview(getEmptyImage(), { captureDraggingState: true });
    }, [preview]);

    return <div ref={drag} style={channelStyle} onClick={onClicked}>
    </div>;
};
