    Name: "Decrement",
    Id: "20",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "bbc" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "0321" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "9321" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "@bc" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "@bc" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "Iello world" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "Hello world" }]
        }
    ],
    Style: {
        Color: "MediumVioletRed",
        BorderColor: "Orange",
    },
    InputChannels: [],
