import { Props } from "./Props";
import React, { useState } from "react";
import { LoadLevel } from "../../../Redux/GameDataReducer/Level/Actions";
import { connect } from "react-redux";
import { LevelState } from "../../../Redux/GameDataReducer/PlayerReducer/LevelData";
import { MachineCanvasHolder } from "../../Grid/Cell/MachineInstance/MachineDrawer/MachineCanvasHolder";
import { GetGrayScaleColor, GetLighterColor, GetOpaqueColor } from "../../../Helper/ColorHelper";
import { Constants } from "../../Constants";
import { MachineStore } from "../../../MachineStore/MachineStore";
import { ChannelModel } from "../../../Models/ChannelModel";

const LevelPreviewInternal: React.FC<Props> = (props) => {
    const OnClick = () => props.LoadLevel(props.Level.Id);
    const [showLabel, setShowLabel] = useState(props.Data?.LastUsedMachineId !== undefined || props.Level.InputChannels.length !== 0);

    let color: string;
    let borderColor: string;

    let outputChannels: ChannelModel[][] = [];

    if (props.Data?.LastUsedMachineId === undefined) {
        color = "rgb(200,200,200)";
        borderColor = "rgb(160,160,160)";
    } else {
        var machine = MachineStore.GetStore().GetMachineType(props.Data!.LastUsedMachineId);
        switch (props.Data.State) {
            case LevelState.Completed:
                color = machine.Style.Color;
                borderColor = machine.Style.BorderColor;
                outputChannels = props.Level.OutputChannels.map((oc) => [oc]);
                break;
            case LevelState.InProgress:
            case LevelState.NonValidMachineUsed:
                color = GetLighterColor(GetGrayScaleColor(machine.Style.Color, 0.2), 0.6);
                borderColor = GetLighterColor(GetGrayScaleColor(machine.Style.BorderColor, 0.2), 0.6);
                break;
            case LevelState.NoMachineLinked:
                throw Error("State is not linked, but there is a machine linked")
        }
    }

    function onHover() {
        if (props.Data?.LastUsedMachineId === undefined && props.Level.InputChannels.length === 0) {
            setShowLabel(true);
        }
    }

    function onExit() {
        if (props.Data?.LastUsedMachineId === undefined && props.Level.InputChannels.length === 0) {
            setShowLabel(false);
        }
    }

    const machineStyle = {
        Color: color,
        BorderColor: borderColor,
    }

    return (<div onMouseOver={onHover} onMouseOut={onExit} style={{ position: "absolute", marginLeft: props.Level.Cell.Column * 140 + 50, marginTop: props.Level.Cell.Row * 115 + 20 }}>
        <div onClick={OnClick}  title={props.Level.LevelDescription} style={{ height: Constants.CellSize - 2, width: Constants.CellSize - 2 }}>
            <MachineCanvasHolder IsDefault={false} MachineStyle={machineStyle} BackgroundColor={"white"} InputChannels={props.Level.InputChannels} OutputChannels={outputChannels} CellSize={Constants.CellSize} />
            {showLabel && <div style={{ display: "flex", position: "absolute", height: "20%", width: "100%", justifyContent: "center", alignItems: "center", pointerEvents: "none" }}>
                <label style={{ fontFamily: "Comic Sans MS", background: GetOpaqueColor("White", 0.7) }}>{props.Level.Name}</label>
            </div>}
        </div>
    </div>);
};

export const LevelPreview = connect(
    undefined,
    { LoadLevel }
)(LevelPreviewInternal);