import { MovingOutputChannel } from "../../Components/DragItemTypes";
import { ScreenType } from "./GameModel";

interface SwitchScreen {
    type: "SwitchScreen";
    screen: ScreenType;
}

export function SwitchScreen(screen: ScreenType): ActionTypes {
    return {
        type: "SwitchScreen",
        screen
    };
}

interface ChangePlayerName {
    type: "ChangePlayerName";
    name: string;
}

export function ChangePlayerName(name: string): ActionTypes {
    return {
        type: "ChangePlayerName",
        name
    };
}

interface UpdateStepTime {
    type: "UpdateStepTime";
    stepTime: number;
}

export function UpdateStepTime(stepTime: number): ActionTypes {
    return {
        type: "UpdateStepTime",
        stepTime
    };
}

interface UpdateShowAnimations {
    type: "UpdateShowAnimations";
    show: boolean;
}

export function UpdateShowAnimations(show: boolean): ActionTypes {
    return {
        type: "UpdateShowAnimations",
        show
    };
}
interface SetMovingOutput {
    type: "SetMovingOutput";
    outputChannel: MovingOutputChannel | undefined;
}

export function SetMovingOutput(outputChannel: MovingOutputChannel | undefined): ActionTypes {
    return {
        type: "SetMovingOutput",
        outputChannel
    };
}
export type ActionTypes = UpdateStepTime | UpdateShowAnimations | SwitchScreen
| ChangePlayerName | SetMovingOutput;