import { OrderedMessageModel } from "../../../Models/OrderedMessageModel";

export const LoadLevelActionType = "LoadLevelActionType";
interface LoadLevelActionType {
    type: typeof LoadLevelActionType;
    id: string;
}

export function LoadLevel(id: string): LevelActionTypes {
    return {
        type: "LoadLevelActionType",
        id
    };
}

export const ReportOutputActionType = "ReportOutputActionType";
interface ReportOutputActionType {
    type: typeof ReportOutputActionType;
    outputMessage: OrderedMessageModel;
    testId: number;
}

export function ReportOutput(outputMessage: OrderedMessageModel, testId: number): LevelActionTypes {
    return {
        type: "ReportOutputActionType",
        outputMessage,
        testId
    };
}
export const ClearTestOutputActionType = "ClearTestOutputActionType";
interface ClearTestOutputActionType {
    type: typeof ClearTestOutputActionType;
    testId: number;
}

export function ClearTestOutput(testId: number): LevelActionTypes {
    return {
        type: "ClearTestOutputActionType",
        testId
    };
}
export const AddTestActionType = "AddTestActionType";
interface AddTestActionType {
    type: typeof AddTestActionType;
}

export function AddTest(): LevelActionTypes {
    return {
        type: "AddTestActionType",
    };
}

export const RemoveTestActionType = "RemoveTestActionType";
interface RemoveTestActionType {
    type: typeof RemoveTestActionType;
    testId: number;
}

export function RemoveTest(testId: number): LevelActionTypes {
    return {
        type: "RemoveTestActionType",
        testId
    };
}

export const UpdateInputActionType = "UpdateInputActionType";
interface UpdateInputActionType {
    type: typeof UpdateInputActionType;
    testId: number;
    input: OrderedMessageModel;
}

export function UpdateInput(input: OrderedMessageModel, testId: number): LevelActionTypes {
    return {
        type: "UpdateInputActionType",
        input,
        testId
    };
}

export const AddInputActionType = "AddInputActionType";
interface AddInputActionType {
    type: typeof AddInputActionType;
    testId: number;
}

export function AddInput(testId: number): LevelActionTypes {
    return {
        type: "AddInputActionType",
        testId
    };
}

export const RemoveInputActionType = "RemoveInputActionType";
interface RemoveInputActionType {
    type: typeof RemoveInputActionType;
    testId: number;
    order: number;
}

export function RemoveInput(testId: number, order: number): LevelActionTypes {
    return {
        type: "RemoveInputActionType",
        testId,
        order
    };
}

export type LevelActionTypes = LoadLevelActionType
    | ReportOutputActionType
    | ClearTestOutputActionType
    | AddTestActionType
    | RemoveTestActionType
    | UpdateInputActionType
    | AddInputActionType
    | RemoveInputActionType;

 