import { ChannelModel } from "../Models/ChannelModel";

export const DragItemTypes = {
  MachineInstance: "MachineInstance",
  NewMachine: "NewMachine",
  OutputChannel: "OutputChannel",
  Grid: "Grid"
};

export interface DraggedOutputChannel {
  type: typeof DragItemTypes.OutputChannel;
  channel: ChannelModel;
  machineInstanceId: number;
  offsetX: number;
  offsetY: number;
}

export interface DraggedNewMachine {
  type: typeof DragItemTypes.NewMachine;
  id: string;
}

export interface MovingOutputChannel extends DraggedOutputChannel {
  angle: number;
}