import { LevelStore } from "../../../Levels/LevelStore";
import { LevelActionTypes } from "./Actions";
import { LevelStateModel } from "./LevelStateModel";
import { TestStateModel } from "./TestStateModel";
import { ChannelColorEnum } from "../../../Models/ChannelColorsEnum";
import { OrderedMessageModel } from "../../../Models/OrderedMessageModel";
import { Draft } from "immer";
import { Actions } from "../../Actions";

export function LevelReducer(draft: Draft<LevelStateModel> | undefined, action: Actions): LevelStateModel | undefined {
    if (action.type === "LoadLevelActionType") {
        return LoadLevel(action.id);
    }

    if (draft === undefined) {
        return undefined;
    }

    switch (action.type) {
        case "AddTestActionType":
            const newTest: TestStateModel = {
                actualOutputs: [],
                expectedOutputs: [],
                inputs: [{ Channel: ChannelColorEnum.Green, Content: "", Order: 0 }],
                editable: true,
                id: Math.max(...draft.tests.map((t) => t.id), -1) + 1
            };
            draft.tests.push(newTest);
            break;
        case "RemoveTestActionType":
            draft.tests = draft.tests.filter((t) => t.id !== action.testId);
            break;
        case "ReportOutputActionType":
        case "ClearTestOutputActionType":
        case "AddInputActionType":
        case "RemoveInputActionType":
        case "UpdateInputActionType":
            const tests = draft.tests.map((t) => {
                if (t.id === action.testId) {
                    return UpdateTest(t, action);
                } else {
                    return t;
                }
            });
            draft.tests = tests;
            break;
        case "AddNewMachineActionType":
        case "ChannelChangedActionType":
        case "DeleteMachineActionType":
        case "FixMachineActionType":
        case "MachineAddedActionType":
        case "MachineMovedActionType":
        case "MachineRemovedActionType":
            draft.tests.forEach((t) => {
                t.actualOutputs = undefined;
            });
            break;
    }

    return draft;
};

function LoadLevel(levelId: string): LevelStateModel {
    const level = LevelStore.GetStore().GetLevel(levelId);
    const tests = level.Tests.length > 0 ? level.Tests.map((t, index) => ({ ...t, actualOutputs: undefined, id: index, editable: false })) :
        [{ actualOutputs: undefined, inputs: [{ Channel: ChannelColorEnum.Green, Content: "", Order: 0 }], expectedOutputs: [], id: 0, editable: true }];
    const returnValue: LevelStateModel = {
        allowedMachines: level.AllowedMachines,
        description: level.LevelDescription,
        name: level.Name,
        id: level.Id,
        tests
    };
    return returnValue;
}

function UpdateTest(test: Draft<TestStateModel>, action: LevelActionTypes): TestStateModel {
    switch (action.type) {
        case "ReportOutputActionType":
            if (test.actualOutputs === undefined) {
                test.actualOutputs = [];
            }
            const outputs = test.actualOutputs.slice();
            outputs.push(action.outputMessage);
            return { ...test, actualOutputs: outputs };
        case "ClearTestOutputActionType":
            return { ...test, actualOutputs: [] };
        case "AddInputActionType":
            const maxOrder = Math.max(...test.inputs.map((i) => i.Order), -1) + 1;
            const newInput: OrderedMessageModel = {
                Channel: ChannelColorEnum.Green, Content: "", Order: maxOrder
            };
            return { ...test, inputs: test.inputs.concat(newInput) };
        case "RemoveInputActionType":

            const filteredInputs = test.inputs.filter((i) => i.Order !== action.order);
            const newInputs = filteredInputs.map((i, index) => ({ ...i, Order: index }));

            const filteredOutputs = test.expectedOutputs.filter((i) => i.Order !== action.order);
            const newOutputs = filteredOutputs.map((o) => ({ ...o, Order: o.Order > action.order ? o.Order - 1 : o.Order }));
            return { ...test, inputs: newInputs, expectedOutputs: newOutputs, actualOutputs: undefined };
        case "UpdateInputActionType":
            const content = action.input.Content.replaceAll("{", "").replaceAll("}", "");
            const inputsUpdated = test.inputs.map((i) => i.Order === action.input.Order ? { ...action.input, Content: content } : i);
            return { ...test, inputs: inputsUpdated };
        default:
            return test;
    }
}
