import React, { useEffect, useState } from "react";
import { useDrag } from "react-dnd";
import { DraggedNewMachine, DragItemTypes } from "../../DragItemTypes";
import { MachineDisplay } from "./MachineDisplay/MachineDisplay";
import { MachineStore } from "../../../MachineStore/MachineStore";
import { LoadCustomMachine, DeleteMachine } from "../../../Redux/GameDataReducer/PlayerReducer/Actions";
import { connect } from "react-redux";
import { Constants } from "../../Constants";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { IsDefaultMachine } from "../../../MachineStore/IMachine";
import { GetActivePlayer } from "../../../Helper/StoreHelper";
import { getEmptyImage } from "react-dnd-html5-backend";
import { IMachineType } from "../../../MachineStore/IMachineType";

const StoredMachineInternal: React.FC<{
    machine: string;
    LoadCustomMachine: typeof LoadCustomMachine;
    DeleteMachine: typeof DeleteMachine;
    status: "Valid" | "Invalid";
    machineType: IMachineType; 
}> = (props) => {
    const [, drag, preview] = useDrag<DraggedNewMachine, unknown, unknown>({
        item: { type: DragItemTypes.NewMachine, id: props.machine },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging(),
        }),
    });
    const [showMenu, changeShow] = useState(false);
    useEffect(() => {
        preview(getEmptyImage(), { captureDraggingState: true })
    }, [preview])

    const isDefault = IsDefaultMachine(props.machine);
    const machineStyle: React.CSSProperties = props.status === "Invalid" ? { borderBottom: Constants.CellSize / 18 + "px dotted orange", boxSizing: "border-box" } : {};

    return (
        <div ref={drag} title={props.machineType.Style.Description} onClick={() => changeShow(!showMenu)}>
            <div style={machineStyle}>
                <MachineDisplay MachineType={props.machineType} IsDefault={isDefault} />
            </div>
            {!isDefault && showMenu && <div style={{ display: "flex" }}>
                <button onClick={(event) => props.LoadCustomMachine(props.machine)}>Edit</button>
                <button onClick={(event) => {
                    props.DeleteMachine(props.machine);
                }}>Delete</button>
            </div>}
        </div>);
};

const mapStateToProps = (state: GameData, ownProps: { machine: string }) => {
    return {
        status: IsDefaultMachine(ownProps.machine) ? "Valid" : GetActivePlayer(state)!.machines.find((m) => m.Id === ownProps.machine)!.Status,
        machineType: MachineStore.GetStore().GetMachineType(ownProps.machine)
    };
};

export const StoredMachine = connect(
    mapStateToProps,
    {
        LoadCustomMachine, DeleteMachine
    }
)(StoredMachineInternal);