import { Computer } from "../Computing/Computer";
import { CleanStars, RestoreStars } from "../Computing/MessageCleaner";
import { MessageModel } from "../Models/MessageModel";
import { GridSetupModel } from "../Redux/GameDataReducer/PlayerReducer/GridSetup/GridModel";
import { AddStarMachine } from "./DefaultMachines/AddStarMachine";
import { AddZeroMachine } from "./DefaultMachines/AddZeroMachine";
import { CompareMachine } from "./DefaultMachines/CompareMachine";
import { CopyMachine } from "./DefaultMachines/CopyMachine";
import { DeleteMachine } from "./DefaultMachines/DeleteMachine";
import { GreenStartMachine } from "./DefaultMachines/GreenStartMachine";
import { IncrementMachine } from "./DefaultMachines/IncrementMachine";
import { MoveMachine } from "./DefaultMachines/MoveMachine";
import { RedStartMachine } from "./DefaultMachines/RedStartMachine";
import { RotateMachine } from "./DefaultMachines/RotateMachine";
import { IActor } from "./IActor";
import { IMachine, IsDefaultMachine } from "./IMachine";
import { IMachineType } from "./IMachineType";
import { AppState } from "../Redux/Store";
import { JoinMachine } from "./DefaultMachines/JoinMachine";
import { SplitMachine } from "./DefaultMachines/SplitMachine";
import { BlueStartMachine } from "./DefaultMachines/BlueStartMachine";
import { YellowStartMachine } from "./DefaultMachines/YellowStartMachine";
import { ITypeProvider } from "./ITypeProvider";
import { GetActivePlayer } from "../Helper/StoreHelper";

export class MachineStore implements ITypeProvider {
    private static machineStore: MachineStore;
    private machines: IMachine[];

    private constructor() {
        this.machines = [];
        this.init();
    }

    public static GetStore(): MachineStore {
        if (MachineStore.machineStore === undefined) {
            MachineStore.machineStore = new MachineStore();
        }

        return MachineStore.machineStore;
    }

    private init(): void {
        this.machines = [];
        this.machines.push(new GreenStartMachine());
        this.machines.push(new RedStartMachine());
        this.machines.push(new BlueStartMachine());
        this.machines.push(new YellowStartMachine());
        this.machines.push(new MoveMachine());
        this.machines.push(new CopyMachine());
        this.machines.push(new DeleteMachine());
        this.machines.push(new RotateMachine());
        this.machines.push(new CompareMachine());
        this.machines.push(new AddStarMachine());
        this.machines.push(new AddZeroMachine());
        this.machines.push(new IncrementMachine());
        this.machines.push(new SplitMachine());
        this.machines.push(new JoinMachine());
    }

    private FindMachine(id: string): IMachine {
        const machine = this.machines.find((m) => m.id === id);
        if (machine === undefined) {
            throw new Error("No default machine found with id " + id);
        }

        return machine;
    }

    public GetActor(id: string, parentStarNumber: number): IActor {
        if (IsDefaultMachine(id)) {
            return this.FindMachine(id).GetActor();
        } else {
            const customMachine = GetActivePlayer(AppState())?.machines.find((cm) => cm.Id === id);
            if (customMachine === undefined) {
                throw new Error("No custom machine found with id " + id);
            }
            return this.CreateCustomActor(customMachine.Setup, parentStarNumber);
        }
    }

    public GetMachineType(id: string): IMachineType {
        if (IsDefaultMachine(id)) {
            return this.FindMachine(id).GetType();
        } else {
            const appState = GetActivePlayer(AppState());
            let customMachine = appState?.machines.find((cm) => cm.Id === id);
            if (customMachine === undefined){
                customMachine = appState.deletedMachines.find((cm) => cm.Id === id);
                if (customMachine === undefined) {
                    throw new Error("No custom machine found with id " + id);
                }
            }

            return customMachine;
        }
    }

    public GetAllMachines(): string[] {
        const machines: string[] = this.machines.map((m) => m.id);
        return machines;
    }

    private CreateCustomActor(setup: GridSetupModel, parentStarNumber: number): IActor {
        const computer = new Computer(setup, false);

        return {
            Act: (message) => {
                const saveMessage = RestoreStars(CleanStars(message.Content, parentStarNumber), computer.starNumber);
                const cleanedMessage: MessageModel = { Content: saveMessage, Channel: message.Channel };

                const computedMessages = computer.RunInput(cleanedMessage);
                const returnValue: Promise<MessageModel[]> = computedMessages.then((messages) => messages.map((mm) => ({ Channel: mm.Channel, Content: RestoreStars(CleanStars(mm.Content, computer.starNumber), parentStarNumber) })));
                return returnValue;
            }
        };
    }
}