import React, { useState } from "react";
import { OrderedMessageModel } from "../../../Models/OrderedMessageModel";
import { OutputStatus } from "../../../Models/OutputStatusEnum";
import { MessageDisplay } from "./MessageDisplay/MessageDisplay";
import { Props } from "./Props";
import { AppStore, AppState } from "../../../Redux/Store";
import { RemoveTest, RemoveInput, UpdateInput, AddInput } from "../../../Redux/GameDataReducer/Level/Actions";
import { connect } from "react-redux";
import { ChannelColorEnum } from "../../../Models/ChannelColorsEnum";
import { Computer } from "../../../Computing/Computer";
import { GetActiveMachine } from "../../../Helper/StoreHelper";

const TestDisplayInternal: React.FC<Props> = (props) => {

    const [error, setError] = useState<string | undefined>(undefined);
    const PlayTest = () => {
        setError(undefined);
        const activeMachine = GetActiveMachine(AppState());

        if (activeMachine !== undefined) {
            const computer = new Computer(activeMachine.Setup, true);
            computer.RunComputation(props.Test.id, AppStore).catch((reason) => {
                setError(reason.toString());
            });
        }
    };

    const inputs = props.Test.inputs;
    const outputs = props.Test.expectedOutputs;
    let messages: JSX.Element[] = [];
    let index = 0;

    for (let i = 0; i < inputs.length; i++) {
        messages.push(
            <div key={index++} style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                <MessageDisplay Message={inputs[i]} Input={true} Status={OutputStatus.NotChecked} Editable={props.Test.editable} AllowedInputColors={[ChannelColorEnum.Green, ChannelColorEnum.Blue, ChannelColorEnum.Red, ChannelColorEnum.Yellow]} Remove={() => props.RemoveInput(props.Test.id, i)} OnMessageChanged={(message) => props.UpdateInput({ ...message, Order: i }, props.Test.id)} />
            </div>
        );
        const expectedOutputs = outputs.filter((o) => o.Order === i);
        let actuals: OrderedMessageModel[] | undefined;

        if (props.Test.actualOutputs !== undefined) {
            actuals = props.Test.actualOutputs.filter((o) => o.Order === i);
        }

        for (let j = 0; j < expectedOutputs.length; j++) {
            const expected = expectedOutputs[j];
            let valid = OutputStatus.NotChecked;
            if (actuals !== undefined) {
                const indexActual = (actuals.findIndex((o) => o.Channel === expected.Channel && o.Content === expected.Content));
                if (indexActual > -1) {
                    valid = OutputStatus.Fullfilled;
                    actuals.splice(indexActual, 1);
                } else {
                    valid = OutputStatus.NotFullfilled;
                }
            }

            /* eslint-disable */
            messages = messages.concat(<MessageDisplay Message={expectedOutputs[j]} Input={false} key={index++} Status={valid} Editable={props.Test.editable} AllowedInputColors={[ChannelColorEnum.Green, ChannelColorEnum.Blue, ChannelColorEnum.Red, ChannelColorEnum.Yellow]}/>);
        }

        if (actuals !== undefined) {
            messages = messages.concat(actuals.map((o) => <MessageDisplay Message={o} Input={false} key={index++} Status={props.Test.editable ? OutputStatus.NotFullfilled : OutputStatus.NotMatched} AllowedInputColors={[ChannelColorEnum.Green]} Editable={false} />));
        }
    }
    /* eslint-enable */

    const style: React.CSSProperties = { display: "flex", flexDirection: "row", borderBottom: "solid 0.5px", marginTop: 5 };
    const verticalStyle: React.CSSProperties = { display: "flex", flexDirection: "column", flexGrow: 1 };

    return (
        <div style={style}>
            {props.Test.editable && <button onClick={() => props.RemoveTest(props.Test.id)}>-</button>}
            <button onClick={PlayTest}>Play</button>
            <div style={verticalStyle}>
                {error !== undefined && <div style={{ color: "red" }}>
                    <label>{error}</label>
                </div>}
                {messages}
                {props.Test.editable && <button style={{ alignSelf: "start" }} onClick={() => props.AddInput(props.Test.id)}>+</button>}
            </div>
        </div>
    );
};

export const TestDisplay = connect(
    undefined,
    { RemoveTest, RemoveInput, UpdateInput, AddInput }
)(TestDisplayInternal);