import { MachineApi } from "../../../Models/MachineApi";
import { MachineInstanceModel } from "../../../Models/MachineInstanceModel";
import { Draft } from "immer";

export function UpdateMachineStatus(api: MachineApi, machineInstance: Draft<MachineInstanceModel>): void {
    for (const newOutputChannel of api.OutputChannels) {
        const matchingOutput = machineInstance.OutputChannels.find((i) => i.Channel === newOutputChannel);
        if (matchingOutput === undefined || matchingOutput.Status === "DoesNotExist") {
            machineInstance.Status = "ChannelsInvalid";
            return;
        }
    }
    for (const outputChannel of machineInstance.OutputChannels.filter((c) => c.Status === "Exists")) {
        if (!api.OutputChannels.some((i) => i === outputChannel.Channel)) {
            machineInstance.Status = "ChannelsInvalid";
            return;
        }
    }
    for (const inputChannel of machineInstance.InputChannels.filter((c) => c.Status === "Exists")) {
        if (!api.InputChannels.some((i) => i === inputChannel.Channel)) {
            machineInstance.Status = "ChannelsInvalid";
            return;
        }
    }
    machineInstance.Status = "Valid";
}