
import { MachineInstancesReducer } from "./MachineInstances/MachineInstancesReducer";
import { CustomMachineModel } from "./CustomMachineModel";
import { Draft } from "immer";
import { Actions } from "../../../Actions";
import { TypeProvider } from "../TypeProvider";
import { CheckColorString } from "../../../../Helper/ColorHelper";
import { GridSetupModel } from "./GridModel";
import { Maximum, Minimum } from "../../../../Helper/ArrayHelper";
import { Constants } from "../../../../Components/Constants";

export function GridSetupReducer(draft: Draft<CustomMachineModel>, action: Actions, typeProvider: TypeProvider, cellSize: number): CustomMachineModel {
    if (draft === undefined) {
        return draft;
    }
    switch (action.type) {
        case "SetColorActionType":
            if (CheckColorString(action.color)) {
                draft.Style.Color = action.color;
            }
            return draft;
        case "SetBorderColorActionType":
            if (CheckColorString(action.borderColor)) {
                draft.Style.BorderColor = action.borderColor;
            }
            return draft;
        case "SetNameActionType":
            draft.Style.Name = action.name;
            return draft;
        case "SetDescriptionActionType":
            draft.Style.Description = action.description;
            return draft;
        default:
            switch (action.type) {
                case "FixMachineActionType":
                    const machineFixed = draft.Setup.MachineInstances.find((mi) => mi.Id === action.machineInstanceId)!;
                    if (machineFixed.Status === "Deleted") {
                        if (draft.Setup.MachineInstances.filter((m) => m.MachineId === machineFixed.MachineId).length <= 1) {
                            draft.DependingOnDeleted = draft.DependingOnDeleted.filter((id) => id !== machineFixed.MachineId);
                        }
                    }
                    break;
            }

            draft.Setup.MachineInstances = MachineInstancesReducer(draft.Setup.MachineInstances, action, typeProvider, cellSize);

            switch (action.type) {
                case "MachineAddedActionType":
                case "MachineRemovedActionType":
                case "MachineMovedActionType":
                    ResizeGrid(draft.Setup);
            }

            return draft;
    }
}

function ResizeGrid(setup: Draft<GridSetupModel>) {
    if (setup.MachineInstances.length > 0) {
        let deltaRow = 0;
        let deltaColumn = 0;
        const minimumColumn = Minimum(setup.MachineInstances, (mi) => mi.Cell.Column)!.Cell.Column;
        const minimumRow = Minimum(setup.MachineInstances, (mi) => mi.Cell.Row)!.Cell.Row;
        let maximumColumn = Maximum(setup.MachineInstances, (mi) => mi.Cell.Column)!.Cell.Column;
        let maximumRow = Maximum(setup.MachineInstances, (mi) => mi.Cell.Row)!.Cell.Row;

        if (minimumColumn <= 0) {
            deltaColumn = 1 - minimumColumn;
            maximumColumn += deltaColumn;
        } else {
            let excessColumns = maximumColumn + 2 - Constants.MinimumColumns;
            if (excessColumns > 0 && minimumColumn > 1) {
                deltaColumn = -Math.min(excessColumns, minimumColumn - 1);
                excessColumns += deltaColumn;
                maximumColumn += deltaColumn;
            }
        }

        if (minimumRow <= 0) {
            deltaRow = 1 - minimumRow;
            maximumRow += deltaRow;
        } else {
            let excessRows = maximumRow + 2 - Constants.MinimumRows;
            if (excessRows > 0 && minimumRow > 1) {
                deltaRow = -Math.min(excessRows, minimumRow - 1);
                excessRows += deltaRow;
                maximumRow += deltaRow;
            }           
        }


        setup.MachineInstances.forEach((mi) => {
            mi.Cell = {
                Row: mi.Cell.Row + deltaRow,
                Column: mi.Cell.Column + deltaColumn
            }
        });

        setup.NumberOfColumns = Math.max(maximumColumn + 2, Constants.MinimumColumns);
        setup.NumberOfRows = Math.max(maximumRow + 2, Constants.MinimumRows);
    }
}