import React from "react";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { Props } from "./Props";

export class InputChannelClickArea extends React.Component<Props> {

    constructor(props: Props) {
        super(props);
        this.onClicked = this.onClicked.bind(this);
    }
    public render() {

        const cellSize = this.props.cellSize - 2;

        const channelStyle: React.CSSProperties = {
            position: "absolute",
            height: cellSize * 0.20,
            width: cellSize * 0.4,
            marginLeft: cellSize * 0.3,
            marginTop: cellSize * 0.05,
            pointerEvents: "all",
        };

        let rotate: string = "";
        switch (this.props.Direction) {
            case DirectionEnum.Left:
                rotate = "rotate(90deg)";
                break;
            case DirectionEnum.Up:
                rotate = "rotate(180deg)";
                break;
            case DirectionEnum.Right:
                rotate = "rotate(270deg)";
                break;
        }

        const rotation: React.CSSProperties = {
            transform: rotate,
            height: cellSize,
            width: cellSize,
            position: "absolute",
            pointerEvents: "none"
        };

        return (
            <div style={rotation}>
                <div style={channelStyle} onClick={this.onClicked}></div>
            </div>
        );
    }

    public onClicked(event: React.MouseEvent<HTMLDivElement, MouseEvent>): void {
        this.props.OnClick(this.props.Direction);
    }
}