import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { LevelModel } from "../LevelModel";

export const JoinAll: LevelModel = {
    AllowedMachines: ["0", "1", "2", "3", "5", "6", "7", "8", "9", "10", "12", "13", "14"],
    LevelDescription: "The machine should store the input from green, appending it to any previously stored message. On red input, it should exit the stored message appended with the input to green, clearing the stored message",
    MachineDescription: "Stores input from green,appending it to any previously stored message. On red input it exits the stored message appended with the input to green",

    Name: "JoinAll",
    Id: "24",
    Tests: [
        {
            inputs: [
                { Order: 0, Channel: ChannelColorEnum.Green, Content: "a" },
                { Order: 1, Channel: ChannelColorEnum.Green, Content: "b" },
                { Order: 2, Channel: ChannelColorEnum.Red, Content: "c" }
            ],
            expectedOutputs: [
                { Order: 2, Channel: ChannelColorEnum.Green, Content: "abc" },
            ]
        },
        {
            inputs: [
                { Order: 0, Channel: ChannelColorEnum.Red, Content: "" },
                { Order: 1, Channel: ChannelColorEnum.Green, Content: "" },
                { Order: 2, Channel: ChannelColorEnum.Red, Content: "ab" },
                { Order: 3, Channel: ChannelColorEnum.Red, Content: "" },
            ],
            expectedOutputs: [
                { Order: 0, Channel: ChannelColorEnum.Green, Content: "" },
                { Order: 2, Channel: ChannelColorEnum.Green, Content: "ab" },
                { Order: 3, Channel: ChannelColorEnum.Green, Content: "" }
            ]
        },
        {
            inputs: [
                { Order: 0, Channel: ChannelColorEnum.Green, Content: "Ma" },
                { Order: 1, Channel: ChannelColorEnum.Green, Content: "rco" },
                { Order: 2, Channel: ChannelColorEnum.Red, Content: "?" },
                { Order: 3, Channel: ChannelColorEnum.Green, Content: "Po" },
                { Order: 4, Channel: ChannelColorEnum.Green, Content: "lo" },
                { Order: 5, Channel: ChannelColorEnum.Red, Content: "!" },
            ],
            expectedOutputs: [
                { Order: 2, Channel: ChannelColorEnum.Green, Content: "Marco?" },
                { Order: 5, Channel: ChannelColorEnum.Green, Content: "Polo!" }
            ]
        },
    ],
    Style: {
        Color: "Grey",
        BorderColor: "YellowGreen",
    },
    InputChannels: [],
    OutputChannels: [{ Channel: ChannelColorEnum.Green, Direction: DirectionEnum.Right, Status: "Exists" },
    ],
    Cell: {
        Row: 4,
        Column: 7
    }
}