import React from "react";
import { useDrop } from "react-dnd";
import { connect } from "react-redux";
import { AreEqual, CellModel } from "../../../Models/CellModel";
import { DirectionEnum } from "../../../Models/DirectionEnum";
import { AddMachine, MoveMachine, ChangeChannel } from "../../../Redux/GameDataReducer/PlayerReducer/GridSetup/MachineInstances/Actions";
import { DragItemTypes, DraggedOutputChannel } from "../../DragItemTypes";
import { FormatMessage } from "../../MessageFormatter";
import { MachineInstance } from "./MachineInstance/MachineInstance";
import { Props } from "./Props";
import { MachineStore } from "../../../MachineStore/MachineStore";
import { GetAngleFromMonitor } from "../../../Helper/AngleHelper";
import { GameData } from "../../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../../Helper/StoreHelper";

const CellInternal: React.FC<Props> = (props) => {

    const [, drop] = useDrop({
        accept: [DragItemTypes.MachineInstance, DragItemTypes.NewMachine, DragItemTypes.OutputChannel],
        drop: (item, monitor) => {
            switch (item.type) {
                case DragItemTypes.MachineInstance:
                    props.OnMovedMachine(monitor.getItem().id, props.cellId);
                    break;
                case DragItemTypes.NewMachine:
                    const itemProps = monitor.getItem() as { id: string };
                    const machineType = MachineStore.GetStore().GetMachineType(itemProps.id);
                    props.OnNewMachine(itemProps.id, machineType.Api, props.cellId);
                    break;
                case DragItemTypes.OutputChannel:
                    const outputItem = monitor.getItem() as DraggedOutputChannel;
                    const angle = GetAngleFromMonitor(monitor, outputItem.offsetX, outputItem.offsetY);
                    if (angle !== undefined) {
                        const adjustedAngle = angle + Math.PI * 2.75;
                        const direction = Math.floor(adjustedAngle / (Math.PI * 0.5)) % 4;
                        props.ChangeChannel(outputItem.machineInstanceId, { Direction: direction, Channel: outputItem.channel.Channel, Status: outputItem.channel.Status }, angle);
                    }
                    break;
            }
        },
    });

    const style: React.CSSProperties = {
        height: props.smallHeigth ? props.CellSize * 0.625 : props.CellSize,
        width: props.smallWidth ? props.CellSize * 0.625 : props.CellSize,
        border: "1px solid black",
        float: "left",
        marginTop: -1,
        marginLeft: -1,
        position: "relative"
    };

    const ExitThickness = props.CellSize * 0.2;

    if (props.Exit !== undefined) {
        switch (props.Exit) {
            case DirectionEnum.Up:
                style.borderRight = "1px solid black";
                style.borderBottom = "1px solid black";
                style.borderLeft = "1px solid black";
                style.borderTop = ExitThickness + "px solid blue";
                style.marginTop = -ExitThickness;
                break;
            case DirectionEnum.Down:
                style.borderRight = "1px solid black";
                style.borderTop = "1px solid black";
                style.borderLeft = "1px solid black";
                style.borderBottom = ExitThickness + "px solid red";
                style.marginBottom = -ExitThickness + 1;
                break;
            case DirectionEnum.Right:
                style.borderTop = "1px solid black";
                style.borderBottom = "1px solid black";
                style.borderLeft = "1px solid black";
                style.borderRight = ExitThickness + "px solid green";
                style.marginRight = -ExitThickness + 1;
                break;
            case DirectionEnum.Left:
                style.borderRight = "1px solid black";
                style.borderBottom = "1px solid black";
                style.borderTop = "1px solid black";
                style.borderLeft = ExitThickness + "px solid #fff020";
                style.marginLeft = -ExitThickness;
                break;
        }
    } else {
        style.borderRight = "1px solid black";
        style.borderBottom = "1px solid black";
        style.borderLeft = "1px solid black";
        style.borderTop = "1px solid black";
    }

    let formattedMessage: string | undefined;
    if (props.Message !== undefined) {
        formattedMessage = FormatMessage(props.Message);
    }

    return (
        <div style={style} ref={drop}>
            {formattedMessage !== undefined && formattedMessage}
            {props.MachineInstanceId !== undefined ? <MachineInstance MachineInstanceId={props.MachineInstanceId} CellSize={props.CellSize - 2} /> : ""}
        </div >
    );
};

const mapStateToProps = (state: GameData, ownProps: { cellId: CellModel }) => {
    const activeMachine = GetActiveMachine(state);
    const machineInstance = activeMachine!.Setup.MachineInstances.find((mi) => AreEqual(mi.Cell, ownProps.cellId));
    const exit = activeMachine!.ExitCells.find((ec) => AreEqual(ec.Cell, ownProps.cellId));
    const message = state.messages.find((m) => AreEqual(m.Cell!, ownProps.cellId));
    const CellSize = GetActivePlayer(state).cellSize;

    return {
        Exit: exit === undefined ? undefined : exit.Direction,
        Message: message === undefined ? undefined : message.Content,
        MachineInstanceId: machineInstance === undefined ? undefined : machineInstance.Id,
        CellSize
    };
};

export const Cell = connect(
    mapStateToProps,
    {
        OnNewMachine: AddMachine,
        OnMovedMachine: MoveMachine,
        ChangeChannel
    }

)(CellInternal);