import { ITypeProvider } from "../../../MachineStore/ITypeProvider";
import { IMachineType } from "../../../MachineStore/IMachineType";
import { CustomMachineModel } from "./GridSetup/CustomMachineModel";
import { MachineStore } from "../../../MachineStore/MachineStore";
import { IsDefaultMachine } from "../../../MachineStore/IMachine";

export class TypeProvider implements ITypeProvider {

    private customMachines: CustomMachineModel[];
    constructor(customMachines: CustomMachineModel[]) {
        this.customMachines = customMachines;
    }
    public GetMachineType(machineId: string): IMachineType {
        if (!IsDefaultMachine(machineId)){
            const customMachine = this.customMachines.find((cm) =>  cm.Id === machineId);
            if (customMachine === undefined){
                throw Error(`CustomMachine not found, Id: '${machineId}'`);
            }

            return customMachine;
        } else {
            return MachineStore.GetStore().GetMachineType(machineId);
        }
    }

}