import { ExitCellModel } from "../Models/ExitCellModel";
import { CellModel } from "../Models/CellModel";
import { MachineInstanceModel } from "../Models/MachineInstanceModel";
import { DirectionEnum } from "../Models/DirectionEnum";
import { GridSetupModel } from "../Redux/GameDataReducer/PlayerReducer/GridSetup/GridModel";
import { Draft } from "immer";
import { ITypeProvider } from "../MachineStore/ITypeProvider";
import { IsStartMachine } from "../MachineStore/IMachine";

export function CalculateExits(setup: Draft<GridSetupModel>, typeProvider: ITypeProvider): ExitCellModel[] {
    const exitCells: ExitCellModel[] = [];
    let cells: ExitCellModel[] = [];
    const starts = GetStartMachines(setup);
    cells = cells.concat(starts.map((s) => ({ Cell: s.Cell, Direction: s.OutputChannels[0].Direction })));
    for (let i = 0; i < cells.length; i++) {
        let cell: CellModel = cells[i].Cell;
        let machine: Draft<MachineInstanceModel> | undefined;
        let cellModel: CellModel | undefined;
        while (machine === undefined) {
            cellModel = Move(setup, cell, cells[i].Direction);
            if (cellModel === undefined) {
                break;
            }

            cell = cellModel;
            // eslint-disable-next-line
            machine = setup.MachineInstances.find((mi) => mi.Cell.Column === cell!.Column && mi.Cell.Row === cell!.Row);
        }

        if (cellModel === undefined) {
            exitCells.push({ Direction: cells[i].Direction, Cell: cell });
            continue;
        }

        const api = typeProvider.GetMachineType(machine!.MachineId).Api;
        // eslint-disable-next-line
        const existingChannel = machine!.InputChannels.find((ic) => ic.Direction === cells[i].Direction);
        if (existingChannel !== undefined) {
            if (!api.InputChannels.some((ic) => ic === existingChannel.Channel)) {
                existingChannel.Channel = api.InputChannels[0];
            }
            existingChannel.Status = "Exists";
        } else {
            machine!.InputChannels.push({
                Channel: api.InputChannels[0],
                Direction: cells[i].Direction,
                Status: "Exists"
            });
        }

        if (!cells.some((c) => c.Cell.Column === cell.Column && c.Cell.Row === cell.Row)) {
            cells = cells.concat(machine!.OutputChannels.map((c) => ({ Cell: cell, Direction: c.Direction })));
        }
    }
    return exitCells;
}

export function Move(setup: {NumberOfRows: number, NumberOfColumns: number}, cell: CellModel, direction: DirectionEnum): CellModel | undefined {
    switch (direction) {
        case DirectionEnum.Down:
            if (cell.Row >= setup.NumberOfRows - 1) {
                return;
            }
            return { Column: cell.Column, Row: cell.Row + 1 };
        case DirectionEnum.Up:
            if (cell.Row <= 0) {
                return;
            }
            return { Column: cell.Column, Row: cell.Row - 1 };
        case DirectionEnum.Right:
            if (cell.Column >= setup.NumberOfColumns - 1) {
                return;
            }
            return { Column: cell.Column + 1, Row: cell.Row };
        case DirectionEnum.Left:
            if (cell.Column <= 0) {
                return;
            }
            return { Column: cell.Column - 1, Row: cell.Row };
    }
}

export function GetStartMachines(setup: GridSetupModel): MachineInstanceModel[] {
    const startMachines: MachineInstanceModel[] = [];
    for (const machineInstance of setup.MachineInstances) {
        if (IsStartMachine(machineInstance.MachineId)) {
            startMachines.push(machineInstance);
        }
    }

    return startMachines;
}