import React from "react";
import { DndProvider } from "react-dnd";
import HTML5Backend from "react-dnd-html5-backend";
import { Game } from "./Components/Game/Game";
import { connect } from "react-redux";
import { GameData } from "./Redux/GameDataReducer/GameModel";
import { Props } from "./Props";
import { StartMenu } from "./Components/StartMenu/StartMenu";
import { LevelSelectScreen } from "./Components/LevelSelectsScreen/LevelSelectScreen";

const AppInternal: React.FC<Props> = (props) => {
  return (
    <DndProvider backend={HTML5Backend}>
      {props.screen === "startMenu" && <StartMenu />}
      {props.screen === "levelSelect" && <LevelSelectScreen />}
      {props.screen === "playing" && <Game />}

    </DndProvider>
  );
};

const mapStateToProps = (state: GameData) => {
  return {
      screen: state.screen,
    };
};

export const App = connect(
  mapStateToProps
)(AppInternal);
