import React from "react";
import { Props } from "./Props";
import { TestDisplay } from "./TestDisplay/TestDisplay";
import { connect } from "react-redux";
import { AddTest } from "../../Redux/GameDataReducer/Level/Actions";
import { SwitchScreen } from "../../Redux/GameDataReducer/Actions";
import { GameData } from "../../Redux/GameDataReducer/GameModel";
import { GetActiveMachine, GetActivePlayer } from "../../Helper/StoreHelper";
import { LevelState } from "../../Redux/GameDataReducer/PlayerReducer/LevelData";
import { AppState, AppStore } from "../../Redux/Store";
import { Computer } from "../../Computing/Computer";

const LevelDisplayInternal: React.FC<Props> = (props) => {

    const tests = props.Level.tests.map((t, index) => <TestDisplay Test={t} key={index} />);

    async function PlayAll() {
        const activeMachine = GetActiveMachine(AppState());

        if (activeMachine !== undefined) {
            for (let test of props.Level.tests) {
                const computer = new Computer(activeMachine.Setup, true);
                const result = await computer.RunComputation(test.id, AppStore).catch((reason) => {
                    return false;
                });

                if (result === false) {
                    break;
                }
            }
        }
    }

    return (
        <>
            <div>
                <b>{props.Level.name}</b>
                <br />
                <i>{props.Level.description}</i>
            </div>
            {tests}
            {props.Level.id === "12" && <><button onClick={props.AddTest}>Add custom test</button><br /></>}
            <button onClick={PlayAll}>Play all tests</button><br />

            {props.Status === LevelState.Completed &&
                <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", marginTop: 10 }} >
                    <b style={{ color: "green" }}>Level completed!</b>
                    <button onClick={() => props.SwitchScreen("levelSelect")}>Select next level</button>
                </div>
            }
            { props.Status === LevelState.NonValidMachineUsed && <b style={{ color: "red" }}>Machine used that is not allowed this level</b>}

        </>
    );
};

const mapStateToProps = (state: GameData) => {
    return {
        Level: state.activeLevel!,
        Status: GetActivePlayer(state)!.levelData.find((l) => l.Id === state.activeLevel!.id)!.State
    };
};

export const LevelDisplay = connect(
    mapStateToProps,
    { AddTest, SwitchScreen }
)(LevelDisplayInternal);